@n-banner-2nd-icon-click-color : #666666;
@n-banner-menu-separator-color : #525151;
@n-banner-2nd-menu-line-height: calc(@n-banner-2nd-gradient-blue-height - 1px);

// White links in banner area
.n-banner-links {

  #gradient > .vertical(@n-banner-2nd-gradient-blue-start, @n-banner-2nd-gradient-blue-end);
  height: @n-banner-2nd-gradient-blue-height;
  padding-top: 0;
  margin-right: 12px;

  > li {
    margin: 0;
    height: @n-banner-2nd-menu-line-height;

    > a {
      color: #FFFFFF;
      font-size: 14px;
      font-weight: normal;
      line-height: 39px;
      padding: 0 10px;
      border-radius: 7px 7px 0 0;
      margin-top: -1px;
      border: 1px solid transparent;
      height: 42px;

      &:active {
        background-color:transparent;
      }

      &:hover, &:focus {
        background: transparent;
        text-shadow: 0 1px 5px rgba(255, 255, 255, 0.75);
        outline: none;
      }

    }

    .dropdown-menu {
      > li > a {
        height: 28px;
        line-height: 28px;
        padding-top: 0;
        padding-bottom: 0;
      }
    }
    .n-banner-span-caret(@n-banner-2nd-icon-color);
    .n-banner-icon-before(@n-banner-2nd-icon-color);

    &.dropdown {

      //menu item vertical line
      &:before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        width: 1px;
        height: @n-banner-2nd-menu-line-height;
        background: linear-gradient(to bottom, #205599, #323232, #205599);
      }

      //move down the text of Menu 1px.
      .icon {
        line-height: @n-banner-2nd-menu-line-height;
      }

      .dropdown-menu {
        border-radius: 0 5px 5px 5px;
        margin-top: -1px;
        border-color: @input-border;
      }

      &.open > a {
        background: @dropdown-menu-background;
        box-shadow: 0 1px 3px 0 #a7a7a7;
        border: 1px solid #b2b2b2;
        color: #333333;
        margin-top: 1px;
        .n-banner-corner-on-drop-down-tab(@nav-tabs-border-color, @dropdown-menu-background);
        &:after {
          height: 12px;
          margin-bottom: -2px;
          z-index: 1001;
        }
        > span:after {
          z-index: 1002;
          #gradient > .vertical(@n-banner-2nd-gradient-blue-start, @n-banner-2nd-gradient-blue-end, -33px);
          margin-bottom: 3px;
        }
        > span:not(:hover) {
          border-bottom: none;
        }
        .n-banner-span-caret(@n-banner-2nd-icon-click-color);
        .n-banner-icon-before(@n-banner-2nd-icon-click-color);
        &:last-child {
          //right aligned with nokia logo
          padding-right: 0;
        }
      }
    }
  }
}

.n-banner-icon-before(@n-banner-2nd-icon-color) {
  .icon:before {
    color: @n-banner-2nd-icon-color;
    vertical-align: middle;
    padding-right: 1px;
    text-shadow: none;
  }
}

.n-banner-span-caret (@n-banner-2nd-icon-color) {
  span.caret {
    border-bottom: none !important;
    color: @n-banner-2nd-icon-color;
    margin-left: 5px;
    padding-bottom: 4px;
  }
}
