@import "../bower_components/bootstrap/less/buttons.less";

@button-text-shadow-white: 0px 1px 0px #fff;
@button-text-shadow-dark: 0px -1px 0px lighten(#000, 10%);
@button-default-border: 1px solid @input-border;
@button-default-dotted-border: 1px dotted @button-focus-hover-border-color;
@button-disabled-border: 1px solid @input-border-disabled;
@button-dark-hover-border: 1px solid #00a1cc;
@button-focus-hover-border-color:#323232;
@button-active-border-color:#a7a7a7;
@button-active-top-border-color:#6b6b6b;
@button-drop-shadow: 0px 1px 3px 0px @button-drop-shadow-color;
@button-drop-shadow-normal:@button-drop-shadow, inset 0px 1px 0px 0px lighten(#fff, 30%);;
@button-drop-shadow-hover:0px 1px 3px 0px @button-drop-shadow-color, inset 0px 0px 0px 1px lighten(@blue-hover, 30%);
@button-drop-shadow-active:inset 0px 1px 1px 0px lighten(#000000, 30%),  inset 0px -1px 0px 0px #eeeeee;
@button-drop-shadow-disabled: 0px 1px 3px 0px @input-border-disabled;
@button-group-drop-shadow-active: @button-drop-shadow, @button-drop-shadow-active;

.btn {
    margin: 4px;

    #gradient > .vertical(@button-basic-gradient-start, @button-basic-gradient-end);
    color: @gray-dark;
    border:@button-default-border;
    text-shadow: @button-text-shadow-white;
    box-shadow:@button-drop-shadow-normal;

    outline: none !important;
    border-radius: 7px;

    &:focus {
        #gradient > .vertical(@button-focus-gradient-start, @button-focus-gradient-end);
        color: @gray-white;
        border-color: @button-active-focus-border-color;
        text-shadow: @button-text-shadow-dark;
        box-shadow: @button-drop-shadow;
    }

    &:hover:not(:focus) {
        #gradient > .vertical(@button-basic-gradient-start, @button-basic-gradient-end);
        color: @gray-dark;
        border-color: @blue-hover;
        text-shadow: @button-text-shadow-white;
        box-shadow: @button-drop-shadow-hover;
    }

    &:hover {
        #gradient > .vertical(@button-focus-gradient-end, @button-focus-gradient-start);
        color:@gray-white;
        border-color: @button-focus-hover-border-color;
        text-shadow: none;
        box-shadow:none;
    }

    &:active {
        #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
        color: @gray-dark;
        border:@button-default-border;
        border-color: @button-active-border-color;
        border-top-color: @button-active-top-border-color;
        text-shadow: @button-text-shadow-white;
        box-shadow: @button-drop-shadow-active;
    }

    &[disabled] {
        color: @input-color-disabled !important;
        #gradient > .vertical(@button-disabled-gradient-start, @button-disabled-gradient-end) !important;
        border: @button-disabled-border !important;
        box-shadow: @button-drop-shadow-disabled !important;
        text-shadow: none !important;
        /*----------when button is disabled, any state will not apply, delete below code in future-------*/
        //&:hover {
        //    border: @button-disabled-border;
        //    box-shadow: @button-drop-shadow-disabled;
        //}
    }

    &-icon,
    &-icon-button{
        height: 32px;
        padding: 6px 8px 8px 8px;

        > span:first-child.icon + span {
            padding-left: 5px;
            margin-right: 12px;
        }


        &:hover, &:focus {
            > .icon:before {
                color: @gray-white;
            }
        }

        &:hover:not(:focus) {
            > .icon:before {
                color: @gray-dark;
            }
        }

        &:active {
            > .icon:before {
                color: @gray-dark;
            }
        }

        &[disabled] {
            > .icon:before {
                color: @gray-light !important;
            }
        }
    }

    &-icon {
        background: none;
        border: 1px solid transparent;
        box-shadow: none;


        &[disabled] {
            background: none !important;
            border: none !important;
            box-shadow: none !important;

            /*----------when button is disabled, any state style will not apply, delete below code in future-------*/
            //&:hover, &:active, &:focus {
            //    border: none;
            //    box-shadow: none;
            //    text-shadow: none;
            //}
        }
    }

    &-large {
        font-size: 18px;
        height: 42px;
        min-width: 104px;
        padding-left: 24px;
        padding-right: 24px;
        border-radius: 7px;
    }

    &-standard {
        font-size: 14px;
        height: 32px;
        min-width: 82px;
        padding-top: 6px;
        padding-left:19px;
        padding-right:19px;
        border-radius: 7px;
    }

    &-small {
        font-size: 14px;
        height: 26px;
        min-width: 70px;
        padding-left: 13px;
        padding-right: 13px;
        padding-top: 2px;
        border-radius: 5px;
    }

    &-close, &-minimize {
        height: 21px;
        width: 21px;
        border-radius: 10px;
        font-weight: bold;
        padding: 1px 0 0 0;
    }

    &-close-dlg {
        height: 18px;
        width: 18px;
        border-radius: 50%;
        padding: 0;
        box-sizing: content-box !important;
    }

    &-close {
        line-height: 21px;
        text-align: center;
        &:focus&:not(:active) {
            .icon-close:before{
                color: #FFFFFF;
            }
        }
    }


    &-close-dlg:after {
        font-family: 'Icons';
        content: "\e604";       // Icon size is 15x14
        font-weight: normal;
        font-style: normal;
        text-decoration: none;
        border-bottom: none;
        line-height: 1em;
    }

    &-minimize:after {
        content: "-";
        top: -2px;
        position: relative;
    }

    &-action {
        margin: 4px;

        #gradient > .vertical(@button-focus-gradient-start, @button-focus-gradient-end);
        color: @gray-white;
        border-left:@button-default-dotted-border;
        border-right:@button-default-dotted-border;
        border-bottom:none;
        border-color:@button-focus-hover-border-color;
        text-shadow: @button-text-shadow-dark;
        box-shadow: @button-drop-shadow;

        &:hover:not(:focus) {
            #gradient > .vertical(@action-button-hover-gradient-start, @action-button-hover-gradient-end);
            color: @gray-white;
            border: @button-dark-hover-border;
            border-color: @input-border-hover;
            text-shadow: none;
            box-shadow: @button-drop-shadow;
        }

        &:active {
            #gradient > .vertical(@button-focus-gradient-end, @button-focus-gradient-start);
            color: @gray-white;
            border-left:@button-default-dotted-border;
            border-right:@button-default-dotted-border;
            border-bottom:none;
            border-color: @button-focus-hover-border-color;
            text-shadow: none;
            box-shadow: none;
        }

        &[disabled] {
            #gradient > .vertical(@action-button-disabled-gradient-start, @action-button-disabled-gradient-end) !important;
            color: #f3f3f3 !important;
            box-shadow: @button-drop-shadow-disabled !important;
            border-color: @action-button-disabled-border-color !important;
            text-shadow: none !important;
            /*----------when button is disabled, any state style will not apply, delete below code in future-------*/
            //&:hover {
            //    box-shadow: @button-drop-shadow-disabled;
            //    border: 1px solid @action-button-disabled-border-color;
            //}
        }
    }

    &-help , &-info{
        height: 18px;
        width: 18px;
        border-radius: 9px;
        padding: 0;
        color: #fff;
        font-weight: bold;
        box-shadow: none;
        text-shadow: none;

        &:focus {
            background: @blue-selection;
            border-color: @blue-selection;
            text-shadow: none;
            box-shadow: none;
        }
        &:hover, &:active {
            background: #085283 !important;
            border: 1px solid @blue-hover !important;
            text-shadow: none !important;
            box-shadow: none !important;
        }

        &[disabled] {
            border: 1px solid @gray-light !important;
            background: @gray-light !important;
        }
        &:after {
            box-shadow: none;
            text-shadow: none;
            position: relative;
            top: -1px;
            color: #ffffff;
        }
    }
    &-help:after {
        content: "?";
    }
    &-info:after{
        content:"i"
    }
    &-help{
        border: 1px solid @blue-selection;
        background: @blue-selection;
    }
    &-info{
        background: @brand-info;
    }
}

.btn-group {
    .btn {
        margin: 0;
        float: left;

        &:first-child {
            .border-right-radius(0px);
        }

        &:last-child {
            .border-left-radius(0px);
        }

        &:not(:last-child):not(:first-child) {
            border-radius: 0;
        }

        &:hover:focus {
            box-shadow: @button-drop-shadow;
        }

        &:active:focus {
            box-shadow: @button-group-drop-shadow-active;
        }
    }

    //TODO: Replace this with a generic reuseable clearfix.
    &:after {
        content: " ";
        clear: both;
        display: block;
        font-size: 0;
        height: 0;
    }

}

.n-tab-buttons{
    .btn.selected{
        .btn-tab-active;

        &:focus{
            border: 1px solid @gray-dark;
        }

        &:hover:not(:focus){
            .btn-tab-active;
        }
    }
}

.btn-tab-active{
    #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
    color: @gray-dark;
    border:@button-default-border;
    border-color: @button-active-border-color;
    border-top-color: @button-active-top-border-color;
    text-shadow: @button-text-shadow-white;
    box-shadow: @button-group-drop-shadow-active;
}

@media print {
    .btn-not-print{
        display: none;
    }
}
