/*---------------- for grid tree table/table ----------------*/
.jqx-grid {
  border-radius: 7px;
  box-shadow: 0 1px 3px 0 #888888;

  /*-------------hover---------------*/
  .jqx-grid-cell-hover {
    background: @n-table-row-white-hover !important;
    color: #000;
  }

  .jqx-grid-cell-selected {
    background: @n-table-row-selected !important;
    color: #FFFFFF;

    a {
      &:link, &:visited, &:hover {
        color: @n-table-row-white;
      }
    }
  }

  /*------------- header -------------*/
  .jqx-widget-header {

    .jqx-grid-column-header.jqx-widget-header {
      background: linear-gradient(@n-table-th-top,@n-table-th-bottom);
      border-color: @n-table-border-color;
      color: #474747;
      font-family: 'Nokia Pure', Helvetica, Arial, sans-serif;
      font-size: 14px;
      > div{
        >div:last-child{
          margin-left: 0px !important;
        }

        .iconscontainer{
          margin-left: -29px !important;
          width: 20px !important;

          .sortasc, .sortdesc{
            background-color: transparent;
            width: 9px !important;
          }

          .filtericon{
            width: 11px !important;
          }
        }

        >div:first-child{
          padding-left: 4px;
          padding-right: 6px;
        }

        .filtericon.jqx-widget-header {
          background: none;
          border-color: transparent;
        }
      }
    }
  }

  /*------------- content -------------*/
  .jqx-widget-content {
    font-size: @font-size-base;
    color: #1B1B1B;
    font-family: 'Nokia Pure', Helvetica, Arial, sans-serif;
    //>div >div[role="row"]{
    //  height:@jqx-line-height !important;
    //}
    .icon {
      line-height: @jqx-line-height;
    }
    .checkbox-small {
      margin-top: 0;
      margin-left: @n-table-margin-left;
      label {
        position: absolute;
        font-size: @font-size-base;
        line-height: @jqx-line-height;
        left: 8px;
        padding-left: 22px;
      }
      label:not(div):before {
        top: 6px;
      }
    }
  }

  /*------------- scrollbar -------------*/
  .jqx-scrollbar-state-normal {
    background-color: #e5e5e5;
    border-radius:4px;
    border-width: 0;

    .jqx-fill-state-normal {
      border: none;
      background: #666;
    }
  }
}

.jqx-grid-cell {
  > div {
    padding-left: 4px !important;
    padding-right: 6px !important;
  }
  .jqx-grid-cell-filter-row {
    margin-left: 1px !important;
  }
  //.checkbox-small label {
  //  margin-left: -14px;
  //}
}

/*---------------- only for grid tree table ----------------*/

.jqx-grid[id^=treeTable] {
  .jqx-widget-header {
    height: 26px !important;
  }

  table.jqx-grid-table {
    width: 100%;
    tr {
      height: 26px;

      .jqx-cell {
        line-height: 26px;
        padding: 0 8px 0 8px;
        border-color: @n-table-border-color;

        > .jqx-tree-grid-collapse-button, .jqx-tree-grid-expand-button {
          vertical-align: baseline;
          margin-right: 3px;
        }
      }

      .jqx-grid-cell-alt {
        background: @n-table-row-grey;
      }

      .jqx-grid-cell-hover {
        background: @n-table-row-white-hover;
        color: #000;
      }

      td[class~="jqx-grid-cell-alt"][class~="jqx-grid-cell-hover"] {
        background: @n-table-row-grey-hover;
      }

      .jqx-grid-cell-selected, td[class~="jqx-grid-cell-selected"][class~="jqx-grid-cell-hover"] {
        background: @n-table-row-selected;
        color: #FFFFFF;
      }
    }

  }
}

.no-grid-line[id^=treeTable] {
  .jqx-grid-table .jqx-grid-cell {
    border-width: 0 0 0 1px;
  }
}

/*---------------- only for grid table ----------------*/
.jqx-grid {
  //checkbox
  .jqx-rc-all {
    border-radius: 4px;
    border: 1px solid #b2b2b2;
  }

  // dropdown
  .jqx-grid-cell {
    .btn-group {
      width: 100%;
      margin: 1px 0 1px 0;
      padding: 0 1px 0 2px !important;
      .dropdown-menu {
        left: 2px;
        min-width: 0px;
        width: calc(100% - 3px);
      }
      .dropdown-toggle {
        width: 100%;
        height: 24px;
        padding-right: 8px;
        padding-left: 8px;
        box-shadow: 0 1px 3px 0 rgba(77, 77, 77, 0);
        border-color: @dropdown-select-caret-border-color;
        &:hover {
          border-color: @blue-hover;
          .selected-caret {
            border-left: 1px solid @dropdown-select-caret-border-color;
          }
        }
        &:focus:not(:hover):not([aria-expanded=true]) {
          color: @dropdown-text;
          text-shadow: none;
          background-image: linear-gradient(to bottom, @button-basic-gradient-start 0%, @button-basic-gradient-end 100%);
        }
        .selected-label {
          float: left;
          font-size:@font-size-base;
          border-right: none;
        }
        .selected-caret {
          float: right;
          .caret {
            border-top: 4px solid @gray-global;
            float: right;
            margin-top: 10px;
            margin-left: @n-table-margin-left;
            margin-bottom: 8px;
          }
        }
      }
    }

    .n-grid-inputfield-indicated {
      padding: 0 !important;
    }
  }

  .jqx-dropdownlist-state-normal {
    border-radius: 6px !important;
    margin: 1px 2px 0 2px !important;
    height:22px !important;
    > div >div {
      background-color: #f3f3f3 !important;
      > div[id^=dropdownlistArrow] {
        border-left: 1px solid #b2b2b2 !important;
        width: 25px !important;
      }
    }
  }
  .jqx-icon {
    width:8px;
    background-image:none;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
    border-top: 4px solid @gray-global;
    margin-top: -2px;
    margin-left: 7px;
  }

  // input
  .n-inputfield-small {
    padding-top: 2px;
    padding-bottom: 2px;
    height: 24px;
    width: calc(100% - 2px);
    margin: 1px 1px 1px 2px;
    box-shadow: none;
  }
}

// dropdownlist jqwiget
.jqx-dropdownlist-content {
  margin:0 !important;
  padding-left: 8px;
  //border-right: 1px solid @dropdown-select-caret-border-color !important;
  > span{
    font-size: @font-size-base;
    font-family: 'Nokia Pure', Helvetica, Arial, sans-serif;
    line-height: 24px;
  }
}

.jqx-listitem-element{
  .jqx-listitem-state-normal {
    padding: 4px 4px 4px 8px;
    font-size:@font-size-base;
    background: #EDEDF0;
    border-radius:0
  }
  .jqx-listitem-state-selected {
    background: #0B6DAF;
    color: #FFFFFF;
  }
  .jqx-listitem-state-hover {
    cursor: pointer;
    border-color: transparent !important;
    background: #FFFFFF;
    color:#0e0e0e;
    > span {
      border-bottom: 1px solid #00a1cc;
    }
  }
}
.jqx-scrollbar-thumb-state-normal {
  background: #666 !important;
  width:7px !important;
}
.jqx-scrollbar-thumb-state-normal-horizontal{
  border-radius: 4px;
}
.jqx-scrollbar-thumb-state-hover, .jqx-scrollbar-thumb-state-hover-horizontal{
  background: #0B6DAF !important;
  border-radius: 4px;
  left:1px;
}


.grid-alternating-rows {
  .jqx-grid-cell {
    border-width: 0 1px 0 0;
  }

  .jqx-grid-cell-alt {
    background: #efefef;
  }
}

/*---------------- for grid table with filter ----------------*/
.jqx-grid-column-filterbutton:extend(.icon) {
  background: none;
  padding-left: 0 !important;
  line-height: 25px;
  &:before {
    content: "\e605";
    color: #474747
  }
}

.jqx-grid-cell {
  .jqx-grid-cell-filter-row {
    background-color: #e0e0e0;
    > div {
      padding: 0 !important;
    }
    input {
      height: @jqx-line-height !important;
      padding: 0 6px;
      border-radius: 7pt;
      margin: 1px !important;
      border: 1px solid #b2b2b2;
      color: #1b1b1b;
      width: calc(100% - 17px) !important;

      &:hover {
        border-color: #00a1cc;
      }
    }

    > .jqx-dropdownlist-state-normal {
      height: @jqx-line-height !important;
      font-family: "Nokia Pure", Helvetica, Arial, sans-serif;

      &:hover {
        border-color: @blue-hover;
        .jqx-dropdownlist-content {
          border-right-color: @blue-hover !important;
        }
      }
      &:focus {
        > div > div {
          border-radius: 6px;
          background-image: linear-gradient(to bottom, @button-focus-gradient-start, @button-focus-gradient-end);
          background-repeat: repeat-x;
          box-shadow: @button-drop-shadow;
          color: @gray-white;

          .jqx-dropdownlist-content {
            height: 26px !important;
            line-height: 26px;
            color: @gray-white;
          }

          .jqx-icon-arrow-down {
            border-top: 4px solid @gray-white;;
          }
        }
      }
      > div > div {
        border-radius: 6px;
        background-image: linear-gradient(to bottom, #f9f9f9 0%, #ebebeb 100%);
        background-repeat: repeat-x;
        color: #1b1b1b;
        box-shadow: 0px 1px 3px 0px #4d4d4d, inset 0px 1px 0px 0px #ffffff;

        .jqx-dropdownlist-content {
          height: 26px !important;
          line-height: 26px;
          color: @gray-global;
        }
      }
    }

    .filter {
      display: none;
    }
  }
}

.jqx-listbox {
  font-family: "Nokia Pure", Helvetica, Arial, sans-serif;
}

.jqx-grid-pager {

  .page-container {
    padding: 0 10px;
    font-family: 'Nokia Pure', Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: @gray-global;
    height:40px;
    #gradient > .vertical(@n-table-th-top, @n-table-th-bottom);
  }

  .n-table-paging-left {
    position: absolute;
    line-height: 40px;
    span {
      color: #474747;
      font-size: 14px;
    }
    .icon-filter{
      display: none;
      padding-right: 3px;
      &:before{
        color: #474747;
      }
    }
    .n-table-filter-result{
      display: none;
      padding-right: 14px;
    }
    &.no-filter{
      >.icon-filter {
        display: inline-block;
        &:before{
          color: #AAA;
        }
      }
    }
    &.has-filter{
      >.icon-filter {
        display: inline-block;
      }
      .n-table-filter-result{
        display: inline-block;
      }
    }
  }

  .n-table-paging-middle {
    text-align: center;
    padding-top: 5px;

    .pageField {
      display: inline-block;
      vertical-align: middle;

      >span:first-child {
        margin: 0 9px 5px;
      }

      .n-inputfield {
        width: 50px;
        border-radius: 7px;
        margin-right: 6px;
        text-align: right;
      }
    }

    .btn-icon {
      width: 28px !important;
      height: 24px !important;
      padding-top: 3px !important;
      padding-left: 6px !important;
    }
  }

  .n-table-paging-right {
    position: absolute;
    top: 7px;
    right: 11px;
    line-height: 25px;

    .jqx-combobox {
      margin-right: 11px;
      float: left;
      border-radius: 7px;

      .jqx-combobox-input {
        text-align: right;
        width: calc(100% - 5px) !important;
        padding: 0 !important;
      }

      div[id*=dropdownlistArrow] :extend(.btn) {
        border-radius: 0 7px 7px 0;
        border-left: none;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .3), inset 0 1px 0 0 #fff;
      }
    }
  }
}

.jqx-listbox {
  border-color: rgba(0, 0, 0, 0.15);
  background-color: #ededf0;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  color: #333333;
  border-radius: 0px 0px 7px 7px;

  .jqx-listitem-element .jqx-listitem-state-selected {
    background: #0b6daf;
    color: white;
    border: none;
    margin: auto;
    margin-top:1px;
    padding-left: 12px;
    &:hover{
      color:inherit;
      background: white;
    }
    > span{
      margin: -2px;
    }
  }
}

div[id^="verticalScrollBar"]{
  border-left: 1px solid #c3c5d0 !important;
  border-bottom: 1px solid #c3c5d0 !important;
  border-top-width: 0 !important;
  border-right-width: 0 !important;
  border-radius: 0 !important;
  padding: 1px;
}

div[id^="horizontalScrollBar"]{
  border-top: 1px solid #c3c5d0 !important;
  border-right: 1px solid #c3c5d0 !important;
  border-bottom-width: 0 !important;
  border-left-width: 0 !important;
  border-radius: 0 !important;
  padding: 1px 0 1px 0;
  height: 10px !important;
}

div[id^="jqxScrollWraphorizontalScrollBar"]{
  height: 8px !important;
  width: 99.5% !important;
  left: 1px !important;
  top: 1px !important;
}
div[id^="jqxScrollWrapverticalScrollBar"]{
  height: 99.5% !important;
  width: 7px !important;
  left: 1px !important;
}

#bottomRight{
  visibility: hidden !important;
}

.n-grid-inputfield-indicated {
  position: relative;
  top: 1px;
  > .n-inputfield {
    width: 100%;
    margin: 0;
    padding: 0 25px 0 @table-cell-padding;
    border-radius: 5px;
  }
  > .form-control-feedback {
    line-height: 28px;
    height: 28px;
    top: -1px;
    right: 8px;
    text-align: right;
  }
}

.grid-checkbox-indicated {
  > label {
    padding-right: 25px;
  }
  .align-right {
    float: right;
  }
  .editor {
    margin-right: 5px;
  }
}

.grid-selectlist-indicated {
  > .form-control-feedback {
    line-height: 28px;
    top: -2px;
    right: 5px;
    text-align: right;
  }
}

.grid-error-header {
  height: 25px;
  width: 100%;
  background-color: @brand-danger-background;
  color: @brand-danger;
  line-height: 25px;
  font-weight: bold;
  border-top: 1px solid #b2b2b2;;

  > span {
    height: 100%;

    &:first-child {
      .icon {
        padding: 0 6px 0 8px;
      }
    }
  }
}

.grid-error-header-icon {
  float: right;
  font-size: 16px;
  padding-right: 6px;
  cursor: pointer;
  line-height: 26px;
  margin-top: -25px;
  .icon:before {
    color: @gray-disabled;
  }
}