@import "../bower_components/bootstrap/less/modals.less";
@import "variables.less";
@import "navbar.less";

// OT3 p.84
// Note 1: these settings are for normal dialogs (not ok for splash screen)
// Note 2: navbar.less produces the banner curve

.modal-content {
  text-align: left;
  #gradient > .vertical(@n-banner-dlg-gradient-gray-end, @n-dialog-gradient-bottom);
  .box-shadow(0px 5px 10px lighten(black, 65%));
}

.modal-header {
  margin: 0;
  padding: 0;
  border-bottom: none;

  .modal-title {
    position: relative;
    padding: 0;
    margin: 20px 0 0 26px;
    z-index: 2;
    color: #1B1B1B;
    font-size: 20px;

    .icon {
      font-size: 16px;
      line-height: inherit;
      margin: 0 6px 0 -6px;
    }
  }
  .n-dialog-header-curve {
    position: absolute;
    left: 140px;
    top: 10px;
    z-index: 1;
    .n-banner-dlg-gray-to-dark;
  }
  .n-dialog-header-fg-filler {
    width: 140px;
    .n-banner-dlg-filler-gray;
  }
  .n-dialog-header-bg-filler {
    .n-banner-dlg-filler-gray-dark;
    border-radius: 6px 6px 0 0;
  }
  .modal-title-subheading {
    color: #474747;
    font-size: 16px;
    font-weight: lighter;
    padding: 6px 0 0 0;
    margin: 0 0 0 26px;
    z-index: 3;
  }
  .modal-title-instruction {
    color: #1B1B1B;
    font-size: 14px;
    padding: 12px 0 0 0;
    margin: 0 0 0 26px;
  }
  .icon-close-rounded {
    position: absolute;
    top: 10px;
    right: 8px;
    font-size: 18px;
    // Need to keep this button as the topmost item in z-order (related to title texts)
    z-index: 4;
    cursor: pointer;
    &:before {
      color: @n-dialog-mini-close-background;
    }
  }
}

@media (max-width: 767px) {
  .modal-header .hidden-xs {
    display: block !important;
  }
}

.modal-body {
  margin: 18px 22px 0 22px;
  padding: 17px 6px 0 6px;
  border-top: 1px solid @n-dialog-internal-divider;
}

.modal-footer {
  border-top: 1px solid @n-dialog-internal-divider;
  margin: 18px 22px 16px 22px;
  padding: 20px 0 0 0;
  color: @gray-global;
  // Need to override Bootstrap default button-button distance settings
  .btn + .btn {
    margin: 4px;
  }
}

.n-dialog-warning {

  .modal-header {

    .n-dialog-header-curve {
      .n-banner-dlg-gray-to-orange;
    }

    .n-dialog-header-fg-filler {
      .n-banner-dlg-filler-gray;
    }

    .n-dialog-header-bg-filler {
      .n-banner-dlg-filler-gray-orange;
      border-radius: 6px 6px 0 0;
    }

    .icon-close-rounded {
      &:before {
        color: @n-dialog-mini-close-white;
      }
    }
  }
}

.n-dialog-critical {

  .modal-header {

    .n-dialog-header-curve {
      .n-banner-dlg-gray-to-red;
    }

    .n-dialog-header-fg-filler {
      .n-banner-dlg-filler-gray;
    }

    .n-dialog-header-bg-filler {
      .n-banner-dlg-filler-gray-red;
      border-radius: 6px 6px 0 0;
    }

    .icon-close-rounded {
      &:before {
        color: @n-dialog-mini-close-white;
      }
    }
  }
}

// +++ About dialog special settings override some modal settings
.n-dialog-about {
  width: 475px;
  .n-about-logo {
    text-align: right;
    padding: 30px 6px 30px 0; // 27 from dialog edge + 45 distance to version and system info
  }
  .n-about-table {
    margin-right: 6px; // 27 distance from dialog edge
    margin-left: 4px; // 26 distance from dialog edge
    line-height: 28px; // 32 but the bottom-padding (needed for vertical text centering) causes stretching
    height: 100%;
  }
  .n-about-table-hrline {
    td, th {
      .box-shadow(inset 0 -1px 0 0 @n-about-hrline-shadow);
      border-bottom: 1px inset @n-about-hrline-border;
      white-space: nowrap;
      color: @gray-global;
      padding-bottom: 4px !important;
    }
    td:last-child {
      white-space: normal;
      width: 99%;
      text-align: right;
    }
  }
  // Override the common modal border settings, no hr lines
  .modal-header {
    .modal-title-subheading {
      padding-top: 1px;
    }
  }
  .modal-body {
    border-top: none;
    border-bottom: none;
    margin: 0 27px 0 27px;
    padding: 0;
  }
  .modal-footer {
    line-height: 57px;
    height: 32px;
    margin: 20px 26px 20px 26px;
    border: none;
    padding: 0;
    .about-close {
      float: right;
      margin-top: 0;
    }
    > table{
      width:100%;
    }
    .about-copyright{
      line-height: 15px;
      vertical-align: bottom;
    }
  }
}

.n-dialog-confirm {
  width: 430px;

  .modal-header {
    .n-dialog-header-fg-filler {
      width: 310px;
    }

    .n-dialog-header-curve {
      left: 310px;
    }

    .modal-title {
      margin-left: 20px;
      width: 320px;
    }

  }

  .modal-body {
    border-top: none;
    margin: 0 20px;
    padding: 15px 0 0;
    line-height: 23px;
  }

  .modal-footer {
    margin-top: 20px;
  }
}

@media (max-width: @screen-sm-min) {
  .modal-content {
    .box-shadow(0 5px 15px rgba(0, 0, 0, .5));
    min-width: 491px;
  }
}

.modal-resize-dialog{
  .modal-content{
    overflow: hidden;
    min-width: 300px;
    min-height: 175px;
    .modal-header{
      .n-dialog-header-resize{
        position: relative;
        top:-10px;
        max-width: 70%;
        width:auto;
        height:auto;
        .n-dialog-header-fg-filler{
          top:0;
          width:100%;
        }
        .n-dialog-header-curve{
          top:0;
          left:auto;
          right:-90px;
        }
        .modal-title {
          padding-top: 10px;
        }
      }
      .modal-title-subheading{
        padding-bottom: 1px;
      }
    }

    .modal-body{
      overflow: hidden;
      min-height: 100px;
    }

    .modal-footer{
      bottom:0px;
      left:0px;
      right:0px;
    }
  }
}

.resizer{
  background: rgb(238,238,238);
  background: -moz-linear-gradient(-45deg,  rgba(238,238,238,1) 50%, rgba(0,0,0,1) 100%);
  background: -webkit-linear-gradient(-45deg,  rgba(238,238,238,1) 50%,rgba(0,0,0,1) 100%);
  background: linear-gradient(135deg,  rgba(238,238,238,1) 50%,rgba(0,0,0,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eeeeee', endColorstr='#000000',GradientType=1 );

}

