@import "../bower_components/bootstrap/less/dropdowns.less";
@import "variables.less";

// OT3 p.128
// Settings work for FuelUX dropdowns
// NOTE: these settings are impacting directly combobox and banner as well

@n-dropdown-box-shadow:0px 1px 3px 0px @button-drop-shadow-color, inset 0px 1px 0px 0px lighten(#fff, 30%);

.btn-group {
    .btn:first-child {
        .border-right-radius(7px);
    }
}

// Button definitions
.dropdown-toggle {
    #gradient > .vertical(@button-basic-gradient-start, @button-basic-gradient-end);
    color: @dropdown-text;
    border: @button-default-border;
    text-shadow: @button-text-shadow-white;
    box-shadow:@n-dropdown-box-shadow;

    .transition(none); //prevents black content in printing together with form-control class
    outline: none !important;
    border-radius: 7px;

    // +++ Add border between text area and arrow
    span.selected-label {
        border-right: 1px solid @input-border;
        padding: 0px 11px 0px 0px;
        height: 100%;
    }
    // +++ Arrow placement
    span.caret {
        margin: 0px 0px 0px 8px;
        border-top: 5px solid;
        color: @dropdown-arrow;
    }

    &:hover:not(:focus){
        span.selected-label {
            border-color: @blue-hover;
        }
        span.selected-caret{
            border-color: @blue-hover;
        }
        span.caret {
            color:@dropdown-arrow;
        }
        span.selected-caret{
            color:@dropdown-arrow;
        }
    }

    &:hover{
        span.selected-label {
            border-color: @gray-dark;
        }
        span.caret {
            color:@gray-white;
        }
    }

    &:focus {
        border-bottom: 1px solid @button-focus-hover-border-color;
        span.selected-label {
            border-color: @gray-dark;
        }
        span.selected-caret {
            border-color: @gray-dark;
        }

        span.caret {
            color:@gray-white;
        }
        span.selected-caret {
            color:@gray-white;
        }
    }

    &:active{
        border:@button-default-border;
        span.selected-label {
            border-color: @input-border;
        }
        span.selected-caret {
            border-color: @input-border;
        }
        span.caret {
            color: @dropdown-arrow;
        }
        span.selected-caret {
            color: @dropdown-arrow;
        }

    }

    // List opened
    &[aria-expanded=true] {
        #gradient > .vertical(@nav-tabs-dark-gradient-top, @nav-tabs-dark-gradient-bottom) !important;
        border-radius: 7px 7px 0px 0px !important;
        text-shadow: none !important;
        color:#333333 !important;
        border:@button-default-border !important;
        border-color: #adadad !important;
        box-shadow: none !important;
        span.selected-label {
            border-color: @input-border !important;
        }
        span.selected-caret {
            border-color: @input-border !important;
        }
        span.caret {
            color: @dropdown-arrow !important;
        }
        span.selected-caret {
            color: @dropdown-arrow !important;
        }
    }


}
// Todo: make the opened list some 10px wider for the longest item (now the dropdown is not as wide as the selection showing
// Todo: for first and last dropdown items the background color shows
// Todo: variants: scrolling


.selectlist-resize {
    width:100%;
    margin:0 0 1px 0;
    .btn
    {
        width: 100%;
    }
    .dropdown-menu
    {
        width:100%;
    }
    .dropdown-toggle {

        &:focus{
            .selected-caret {
                border-left: 1px solid @gray-dark;
            }
            &[aria-expanded=true]{
                .selected-caret {
                    border-left: 1px solid @input-border;
                }
            }
        }

        &:hover:not(:focus){
            .selected-caret {
                border-left: 1px solid @blue-hover;
            }
        }

        &:hover{
            .selected-caret {
                border-left: 1px solid @gray-dark;
            }

            &[aria-expanded=true]{
                .selected-caret {
                    border-left: 1px solid @input-border;
                }
            }
        }

        &:active{
            .selected-caret {
                border-left: 1px solid @input-border;
            }
        }

        .selected-label {
            float: left;
            border-right: none;
        }
        .selected-caret {

            float: right;
            border-left: 1px solid #b2b2b2;
            .caret {
                float: right;
                margin-top: 13px;
                margin-left: 10px;
                margin-bottom: 12px;
            }
        }
    }
}

.selectlist-small {
    .dropdown-toggle {
            span.caret {
                margin: 0 0 0 6px;
            }
    }
}


.dropdown-menu, .dropdown-menu-right {
    background-color: @dropdown-menu-background;
    color: @dropdown-menu-color;
    border-radius: 0px 0px 7px 7px;
    margin-top: -1px;
    // +++ Previously selected item in dropdown list with blue background
    // Last two items are for combobox dropdown case
    li[data-selected=true] > a, li[data-selected=true] > .n-a, li.selected > a, li.selected > .n-a {
        background-color: @blue-selection;
        color: white;
    }
    li > a, li > .n-a {
        height: 27px;
        border-bottom: 1px solid transparent;
        padding-left: 11px;
        // +++ Slightly grey the not-selected list items
        background-color: @dropdown-menu-background;
        color: @dropdown-menu-color;
        // +++ Hover effects on the opened list, also used for the keyboard focusing effect
        &:hover, &:focus {
            background-color: white;
            color: black;
            // +++ Get rid of outline for Moz
            outline: none;
            // +++ Blue underline on hovered item
            > span {
                border-bottom: 1px solid @blue-hover;
            }
        }
    }
}

.n-dropdown-menu-scroll{
    max-height: 150px;
    li > a {
        display: block;
        padding: 3px 20px;
        clear: both;
        font-weight: normal;
        line-height: @line-height-base;
        color: @dropdown-link-color;
        white-space: nowrap; // prevent links from randomly breaking onto new lines

        &:hover,
        &:focus {
            text-decoration: none;
            color: @dropdown-link-hover-color;
            background-color: @dropdown-link-hover-bg;
        }
    }

    >.mCustomScrollBox{
        padding: 0;
        width: 100%;
        border-radius:0;
        background-color: transparent;

        >.mCSB_container{
            margin-right: 0;
        }
    }
}

.selectlist {
    display:-moz-inline-box !important;
}

.selectlist .dropdown-toggle {
    padding-top: 0;
    padding-bottom: 0;

   display: inline-flex;
    .selected-caret {
        float: right;
        border-left: 1px solid #b2b2b2;
        .caret {
            float: right;
            margin-top: 13px;
            margin-left: 12px;
            margin-bottom: 12px;
            height: 5px;
        }
    }
}

.selectlist-resize .dropdown-toggle{
    display: inline-block;
}

.selectlist-small .dropdown-toggle{
    display: inline-block;
}

.selectlist .dropdown-toggle>.selected-label {
    height: @n-dropdown-standard-height;
    line-height: @n-dropdown-standard-height;
    display: inline-block;
    float: left;
    border-right: none;
}

// +++ Small variant
.selectlist-small {
    .dropdown-toggle {
        border-radius: 6px;
        font-size: @font-size-small;
        padding-left: 11px;
        padding-right: 10px;
        span.selected-label {
            height: @selectlist-small-height;
            line-width: 28px;
            line-height: @selectlist-small-height;
            padding: 0px 10px 0px 0px;
        }
        &[aria-expanded=true] {
            border-radius: 6px 6px 0px 0px;
        }
    }
    .dropdown-menu {
        border-radius: 0px 0px 6px 6px;
        font-size: @font-size-small;

        >li>a{
            padding-top: 6px;
        }
    }
}

.selectlist-small {
    .dropdown-toggle {
        .caret{
            margin: 0 0 0 6px;
        }
        .selected-caret {
            .caret {
                margin-top: 10px;
                margin-left: 10px;
                margin-bottom: 9px;
            }
        }
    }
}

// +++ Dark variant
.selectlist-dark {
    .dropdown-toggle {
        #gradient > .vertical(@dropdown-dark-focus-gradient-start, @dropdown-dark-focus-gradient-end);
        color: white;
        text-shadow: none;
        box-shadow: 0px 1px 3px 0px @tooltip-border-shadow !important;

        span.selected-caret {
            border-left: 1px solid black;
        }
        span.caret {
            color: @dropdown-arrow-dark;
        }
        &[aria-expanded=true] {
            #gradient > .vertical(@dropdown-dark-focus-gradient-end, @dropdown-dark-focus-gradient-start) !important;
            color:@gray-white !important;
            .selected-label {
                border-color: @gray-dark !important;
            }
            span.selected-caret {
                border-color: @gray-dark !important;
            }
            span.caret {
                color: @dropdown-arrow-dark !important;
            }
        }

        &:hover:not(:focus){
            #gradient > .vertical(@dropdown-dark-hover-gradient-start, @dropdown-dark-hover-gradient-end);
            color: @gray-white;
            .selected-caret {
                border-color: @blue-hover;
            }
            span.caret {
                color: @dropdown-arrow-dark;
            }
        }

        &:hover{
            #gradient > .vertical(@dropdown-dark-focus-gradient-end, @dropdown-dark-focus-gradient-start);
            color: @gray-white;
            .selected-label {
                border-color: @gray-dark;
            }
        }

        &:active {
            #gradient > .vertical(@button-action-active-gradient-start, @button-action-active-gradient-end);
            color: @gray-white;
            border-left:@button-default-dotted-border;
            border-right:@button-default-dotted-border;
            border-color: @button-active-focus-border-color;
            text-shadow: @button-text-shadow-dark;
            box-shadow: none;
            .selected-label {
                border-color: @gray-dark;
            }

        }
    }
    .dropdown-menu {
        background-color: @dropdown-dark-menu-item;
        color: white;
        margin-top: -2px;
        li[data-selected=true] > a, li[data-selected=true] > .n-a {
            background-color: @blue-selection;
            color: white;
        }
        li > a, li > .n-a {
            // +++ Not-selected list items
            background-color: @dropdown-dark-menu-item;
            color: white;
            &:hover, &:focus {
                background-color: @dropdown-dark-menu-item-hover !important;
                color: white;
                // +++ Blue underline on hovered item
                > span {
                    border-bottom: 1px solid @blue-hover;
                }
            }
        }
    }
}

.selectlist-indicated{
    > .form-control-feedback{
        line-height: 28px;
        right: 32px;
        text-align: right;
    }
}

// Disabled cases
.btn-group.selectlist.disabled {
    outline: none !important;
    color: @input-color-disabled;
    span.selected-label {
        border-right: 1px solid @button-disabled-border;
    }
    span.caret {
        color: @dropdown-dark-disabled-text;
    }
}



.selectlist-multiple {
    .dropdown-toggle {
        .selected-label {
            width: 132px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
    .dropdown-menu {
            width:177px;
            li > a {
                padding-right:0px;
                &:hover, &:focus {
                    > span {
                        border-bottom: 0;
                    }
                    > span > label {
                        width:100%;
                        > span{
                            border-bottom: 1px solid #00a1cc;
                        }
                    }
                }
            }
            li[data-selected=true] > a{
                color:inherit;
                background-color: inherit;
                &:hover{
                    color: black;
                    background-color: white;
                    outline: none;
                }
            }
            .checkbox{
                padding-top:0;
            }
    }
}


span.n-small-caret {
    margin: 0px 0px 0px 6px;
}

@media print {
    .dropdown-toggle {
        background-color: #d3d3d3 !important;
    }
}

