@import "../bower_components/bootstrap/less/buttons.less";
@import "../bower_components/bootstrap/less/mixins/gradients";
@import "variables";

@n-toggle-switch-border-radius-standard: 7px;
@n-toggle-switch-border-radius-small: 5px;
@n-toggle-switch-width-standard: 83px;
@n-toggle-switch-width-small: 63px;
@n-toggle-switch-height-standard: 32px;
@n-toggle-switch-height-small: 22px;
@n-toggle-switch-slider-width-standard: 41px;
@n-toggle-switch-slider-width-small: 31px;

.n-toggle-switch {


  input[type=checkbox] {
    -moz-opacity: 0;
    opacity: 0;
    margin: 0;
    //no opacity for IE to get normal checkbox visible
    //filter: alpha(opacity=0);
  }
  margin: 4px;
  position: relative;
  user-select: none;

  .n-toggle-switch-container {
    display: block;
    cursor: pointer;
    height: inherit;
    overflow: hidden;

    .n-toggle-switch-handler {
      display: block;
      color: #ffffff;
      margin: 0 0 0 -100%;
      width: 200%;

      &:before {
        content: "Off";
      }

      &:after {
        content: "On";
        text-align: right;
      }

      &:before,
      &:after {
        background-repeat: repeat-x;
        box-shadow: none;
        box-sizing: border-box;
        display: block;
        float: left;
        height: 32px;
        padding: 0;
        text-shadow: @button-text-shadow-dark;
        width: 50%;
        #gradient.vertical(@n-toggle-switch-bg-grey-top, @n-toggle-switch-bg-grey-bottom);
      }

    }

    .n-toggle-switch-slider {
      background: #ffffff;
      border: 1px solid #b6b6b6;
      display: block;
      margin: 1px;
      position: absolute;
      top: -1px;
      transition: all 0.1s ease-in 0s;

      &:hover + .n-toggle-switch-container {
        border: 1px solid #00a1cc;
        box-shadow: 0 1px 3px 0 #4d4d4d, inset 0 0 0 1px #66dfff;
      }

      &:active + .n-toggle-switch-container {
        border-color: #000000;
        box-shadow: 0 1px 3px 0 #000000;
        color: #f3f3f3;
        text-shadow: none;
        #gradient.vertical(#afafaf, #8f8f8f);
      }

      &:focus {
        border: 1px solid #000000;
      }
    }
  }

  .n-toggle-switch-input {
    display: block;
    position: absolute;

    &:checked + .n-toggle-switch-container {
      .n-toggle-switch-handler {
        margin-left: -2px;
      }

      .n-toggle-switch-slider {
        margin-right: 0;
        right: 0;
      }
    }

    &:focus + .n-toggle-switch-container {
      .n-toggle-switch-slider {
        border-color: #323232;
      }
    }

    &:active:enabled + .n-toggle-switch-container {
      .n-toggle-switch-slider {
        background-repeat: repeat-x;
        text-shadow: none;
        #gradient.vertical(@n-toggle-switch-bg-light-grey-top, @n-toggle-switch-bg-light-grey-bottom);
      }
    }

    &[disabled] + .n-toggle-switch-container {
      cursor: default;
      pointer-events: none;

      .n-toggle-switch-handler {
        &:before,
        &:after {
          border: 1px solid @n-toggle-switch-bg-light-grey-border;
          box-shadow: none;
          text-shadow: none;
          #gradient.vertical(@n-toggle-switch-bg-light-grey-top, @n-toggle-switch-bg-light-grey-bottom);
        }
      }

      .n-toggle-switch-slider {
        background-color: #f3f3f3;
        border-color: #cccccc;
      }
    }
  }

  // set size for a standard toggle button
  &-standard {
    width: @n-toggle-switch-width-standard;
    height: @n-toggle-switch-height-standard;

    .n-toggle-switch-container {
      border-radius: @n-toggle-switch-border-radius-standard;

      .n-toggle-switch-handler {
        font-size: 14px;
        line-height: @n-toggle-switch-height-standard;

        &:before {
          padding-left: 12px;
        }

        &:after {
          padding-right: 15px;
        }
      }

      .n-toggle-switch-slider {
        border-radius: @n-toggle-switch-border-radius-standard;
        height: @n-toggle-switch-height-standard;
        right: @n-toggle-switch-slider-width-standard;
        width: @n-toggle-switch-slider-width-standard;
      }
    }
  }

  // set size for a small toggle button
  &-small {
    width: @n-toggle-switch-width-small;
    height: @n-toggle-switch-height-small;

    .n-toggle-switch-container {
      border-radius: @n-toggle-switch-border-radius-small;

      .n-toggle-switch-handler {
        font-size: 11px;
        line-height: @n-toggle-switch-height-small;

        &:before {
          padding-left: 9px;
        }

        &:after {
          padding-right: 10px;
        }
      }

      .n-toggle-switch-slider {
        border-radius: @n-toggle-switch-border-radius-small;
        height: @n-toggle-switch-height-small;
        right: @n-toggle-switch-slider-width-small;
        width: @n-toggle-switch-slider-width-small;
      }
    }
  }

  // paint color to toggle button
  &-color {
    .n-toggle-switch-container {
      .n-toggle-switch-handler {
        &:before {
          border-color: @n-toggle-switch-bg-red-border;
          #gradient.vertical(@n-toggle-switch-bg-red-top, @n-toggle-switch-bg-red-bottom);
        }

        &:after {
          border-color: @n-toggle-switch-bg-green-border;
          #gradient.vertical(@n-toggle-switch-bg-green-top, @n-toggle-switch-bg-green-bottom);

        }
      }
    }

    // handle disabled colors
    .n-toggle-switch-input[disabled] + .n-toggle-switch-container {
      .n-toggle-switch-handler {
        &:after {
          border-color: @n-toggle-switch-bg-light-green-border;
          #gradient.vertical(@n-toggle-switch-bg-light-green-top, @n-toggle-switch-bg-light-green-bottom);
        }

        &:before {
          border-color: @n-toggle-switch-bg-light-red-border;
          #gradient.vertical(@n-toggle-switch-bg-light-red-top, @n-toggle-switch-bg-light-red-bottom);
        }
      }
    }
  }
}