@import "variables.less";

// OT3 p.129
// Settings work for FuelUX combobox
// NOTE: see also the dropdowns.less + inputfields.less as those styles are used for combobox

.combobox {
  white-space: nowrap;
  .form-control.n-inputfield {
    box-shadow: none;
    &:hover:enabled{
      box-shadow: inset 0 0 0 1px lighten(@input-border-hover, 30%);
    }
    &:focus{
      box-shadow: none;
    }
  }

  .form-control
  {
    height: 30px;
  }

  &.combobox-open > .form-control.n-inputfield {
    border-bottom-left-radius: 0;

  }
}

.combobox .dropdown-toggle, .n-combobutton .dropdown-toggle {
  border-radius: 0 7px 7px 0 !important;
  //TODO temporary margin
  margin: 0;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .3), inset 0 1px 0 0 #fff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .3), inset 0 1px 0 0 #fff;
  height:30px;
  padding-top: 4px;
  padding-bottom: 4px;
  // +++ Arrow placement
  span.caret {
    margin: 0 0 0 0;
  }
  &[aria-expanded=true] {
    border-radius: 0 7px 0 0 !important;
    #gradient > .vertical(@nav-tabs-dark-gradient-top, @nav-tabs-dark-gradient-bottom) !important;
    color: @gray-dark !important;
    border-color: @button-active-border-color !important;
    border-top-color: @button-active-top-border-color !important;
    text-shadow: @button-text-shadow-white !important;
    box-shadow: @button-drop-shadow-active !important;
  }

  &:focus{
    border-bottom: none;
    box-shadow: @button-drop-shadow;
  }

  &:hover:not(:focus){
    box-shadow: @button-drop-shadow-hover;
  }

  &:active {
    border-bottom:@button-default-border;
    box-shadow: @button-drop-shadow-active;
  }

  &.disabled{
    border:1px solid #b2b2b2;
  }

}

.n-combobutton .dropdown-toggle
{
  padding-left: 11px !important;
  padding-right: 11px !important;
  // Float setting is needed to keep button with the text field in responsive case
  float: none;
}

.n-combobutton-btn {
  padding-right: 36px;
  padding-top: 4px;
  min-width: 160px;
  .selected-label {
    float: left;
  }
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .3), inset 0 1px 0 0 #fff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .3), inset 0 1px 0 0 #fff;
  height: 30px;
}

.n-combobutton .dropdown-menu {
  width: 100%;
}

.n-scrollable {
  overflow-y: scroll;
}

// +++ Small variant
.n-combobutton-small {
  .n-combobutton-btn,
  .dropdown-toggle {
    font-size: @font-size-small;
    height: 26px !important;
    padding-left: 10px !important;
    padding-right: 10px !important;
    padding-top: 2px;
    border-radius: 5px;
    span.selected-label {
      height: 26px;
      line-height: 23px;
      padding: 0 10px 0 0;
    }
    &[aria-expanded=true] {
      border-radius: 6px 6px 0 0;
    }
  }
  .dropdown-menu {
    border-radius: 0 0 6px 6px;
    font-size: @font-size-small;

    >li>a{
      padding-top: 6px;
    }
  }
}

.n-combobutton.open > .btn {
  border-bottom-left-radius: 0;
}

.btn-group.n-combobutton  {
  // Following needed to keep the button together with the textfield in responsive situation
  white-space: nowrap;
}

.combobox-filter-dropdown-menu {
  min-height: 39px;
}

.combobox-item-hidden {
  display: none;
}




