// Container to keep the label aligned with the actual spinner component
.spinner-container {
    display: table;

    > label {
        display: table-cell;
        vertical-align: middle;
        // Consider using nowrap, but probably we should leave this setting to customers to make
        //white-space: nowrap;
    }

    //TODO responsive behavior needs work for the spinner
/* Responsive situation testing (note: the block statement below causes vertical alignment is lost for the label)
    @media screen and (max-width: 479px) {
        .spinner-container {
            display: block;
        }
        label {
            display: inline-block;
        }
    }
    */

    .spinbox {
        display: inline-block;
        position: relative;

        // TODO this padding-top added to get the related label alignment correctly on the same level
        // label has for some reason always extra 5px in vertical size compared to the spinner, reason not known
        padding-top: 4px;
        .spinbox-input {
            float: left;
            width: 110px;
            height: 34px;
            padding: 0px 11px;
            font-weight: bold;
            font-size: 14px;
            -webkit-box-shadow: none;
            box-shadow: none;

            &::selection {
                background-color: #0B6DAF;
            }

            &::-ms-clear {
                display: none;
            }
        }

        .spinbox-buttons {
            position: absolute;
            width: 32px;
            // TODO this padding-top added to get the related label alignment correctly on the same level
            // label has for some reason always extra 5px in vertical size compared to the spinner, reason not known
            padding-top: 4px;
            top: 0;
            bottom: 0;
            right: 0;
            margin: 0;

            .btn {
                position: relative;
                height: 17px;
                width: 32px;
                margin: 0;
                line-height: 15px;

                &:focus {
                    .icon:before {
                        color: #ffffff;
                    }
                }
                &:active {
                    .icon:before {
                        color: #000000;
                    }
                }
            }

            .spinbox-up {
                border-top-right-radius: 7pt !important;
                border-top-left-radius: 0 !important;
            }

            .spinbox-down {
                border-top-right-radius: 0 !important;
                border-top-left-radius: 0 !important;
                border-bottom-right-radius: 7pt;
                border-bottom-left-radius: 0 !important;
            }

            .icon:focus:before {
                color: red;
            }
        }
    }
}


