.n-dlg-wizard {

  .modal-body {
    padding: 0;
    border-top: none;
  }

  .navbar {
    min-height: 40px;
    padding: 0 4px;
    .navbar-inner > ul {
      li {
        > a {
          height: 40px;
          width: 40px;
          border-radius: 40px;
          background-image: radial-gradient(circle, #f1f1f1, #E0E0E0, #A9A9A9);
          padding: 0;
          pointer-events: none;

          > span {
            display: inline-block;
            height: 28px;
            width: 28px;
            border-radius: 28px;
            font-weight: bold;
            font-size: 16px;
            background-color: #f7f7f7;
            margin: 6px 0 0 6px;
            line-height: 28px;
            color: #000;
            padding: 0;
            text-align: center;
            border: 1px solid #c7c9c9;
          }
        }

        &.active:not(:last-child)::before {
          content: "";
          position: absolute;
          left: 40px;
          top: 16px;
          width: 10px;
          border-top: 1px solid #c7c9c9;
          border-bottom: 1px solid #c7c9c9;
          #gradient.vertical(#0a75b2, #69b2cb);
          height: 7px;
          border-radius: 0 4px 4px 0;
          z-index: 100;
        }

        &:not(:last-child)::after {
          content: "";
          position: absolute;
          left: 40px;
          right: -2px;
          top: 16px;
          border-top: 1px solid #c7c9c9;
          border-bottom: 1px solid #c7c9c9;
          #gradient.vertical(#C1C1C1, #f1f1f1);
          height: 7px;
        }

        &.active, &.passed {
          > a {
            background-image: linear-gradient(to bottom, #0a75b2, #69b2cb);
          }
        }

        &.passed::after {
          background-image: linear-gradient(to bottom, #0a75b2, #69b2cb);
          transition: background-image 0.2s ease-in-out;
        }
      }
    }
  }

  .tab-content {
    border: none;
    padding: 0;
    box-shadow: none;
    background: none;

    .n-dlg-wizard-step-description {
      padding: 0 4px 18px;
      border-bottom: 1px solid #d8d8d8;
    }

    .n-dlg-wizard-step-content {
      padding: 18px 4px 0;
    }
  }

  .modal-footer {
    .btn.disabled:focus {
      color: #1b1b1b;
      #gradient.vertical(#f9f9f9, #ebebeb);
      border-color: #b2b2b2;
      border: 1px solid #b2b2b2;
      box-shadow: none;
      text-shadow: none;
    }
  }
}