.n-drillDown-content-row{
  .icon-close-rounded{
    position: absolute;
    z-index: 3;
    top: 0;
    right: 30px;
    margin-top: 30px;
    cursor: pointer;
  }
  //border: 1px solid rgba(255,255,255,.65);
  border-top: 1px solid rgba(255,255,255,.65);
  border-bottom: 1px solid rgba(255,255,255,.65);
  border-top: 0px;
  position: relative;
  width: 100%;
  height: 100%;
}


.n-drillDown-collapsed-row{
  display: none;
  position: relative;
  width: 100%;
}

.n-drillDown-content-row{
  .n-drillDown-inner:before{
    content:'';
    background: -webkit-linear-gradient(rgba(0,0,0,.2) , rgba(0,0,0,0));
    background: -o-linear-gradient(rgba(0,0,0,.2), rgba(0,0,0,0));
    background: -moz-linear-gradient(rgba(0,0,0,.2), rgba(0,0,0,0));
    background: linear-gradient(rgba(0,0,0,.2) , rgba(0,0,0,0));
    position: absolute;
    top:0px;
    left:0px;
    height: 70px;
    width:100%;
  }

  .n-drillDown-inner{
    padding:30px;
    background: #D8D9DA;
    height: 100%;
    width:100%;
    box-shadow: 12px 0 7px -12px rgba(0, 0, 0, .45) inset,-12px 0 7px -12px rgba(0, 0, 0, .45) inset,0 12px 30px -12px rgba(0, 0, 0, .6) inset,0 -12px 30px -12px rgba(0, 0, 0, .2) inset;
  }

  .n-drillDown-inner:after{
    content:'';
    position: absolute;
    bottom:0px;
    left:0px;
    height: 70px;
    width:100%;
  }
}






