.fuelux .n-calendar {
  button {
    &:active {
      outline: none;
    }
    &:focus {
      outline: none;
    }
  }
}

.fuelux {
  .datepicker-calendar-wrapper {
    button {
      &:focus {
        border-bottom: 1px solid @blue-hover;
      }
    }
  }
}


.fuelux {
  .n-calendar {
    .input-group-btn {
      width: inherit;
    }
  }
}

.fuelux {
  .n-calendar {
    .form-control {
      border-left-width: 1px;
    }
    .input-group-btn {
      .btn-default {
         margin: 0;
       }
      .dropdown-toggle {
        border-top-left-radius: 0 !important;
        margin-top: 0;
      }
      .glyphicon-calendar {
        font-family: 'Icons';
        width: 1em;
        &:before {
          content: '\e636';
        }
      }
    }
  }
}

.fuelux {
  .n-calendar {
    .form-control {
      &:hover {
        border: 1px solid @blue-hover;
        box-shadow: inset 0 0 0 1px @calendar-input-box-shadow;
      }
      &:focus {
        border: 1px solid @gray-medium;
        box-shadow:inset 0 1px 1px rgba(0, 0, 0, .075)
      }
      &[disabled] {
        border: 1px solid #CCC;
        border-right: none;
        box-shadow: none;
      }
    }
  }
}

.fuelux {
  .datepicker-calendar {
    padding: 16px 8px 8px;
    min-height:0;
  }
}

.fuelux {
  .datepicker-calendar-header {
    .pre-next-commonstyle(@font-family, @content, @margin-left, @margin-right,@color) {
      font-family: @font-family;
      content: @content;
      margin-left: @margin-left;
      margin-right: @margin-right;
      color: @color;
    }
    .title {
      text-decoration: none;
      width: auto;
      line-height: 26px;
      display:table;
      &:hover {
        color: @blue-selection;
        text-decoration: none;
        border-bottom: 1px solid @blue-hover;
      }
    }
    .next {
      background: none;
      & :before {
        .pre-next-commonstyle('Icons',"\e612",1px,1px,@calendar-text);
      }
      &:hover {
        background: none;
      }
      &:hover :before{
        color:@blue-selection;
      }
      &:focus{
        border-bottom:none;
      }
      &:focus :before{
        color:@blue-selection;
      }
    }
    .prev {
      background: none;
      & :before {
        .pre-next-commonstyle('Icons',"\e61e",1px,1px,@calendar-text);
      }
      &:hover {
        background: none;
      }
      &:hover :before{
        color:@blue-selection;
      }
      &:focus{
        border-bottom:none;
      }
      &:focus :before{
        color:@blue-selection;
      }
    }
  }
}

.fuelux {
  .datepicker-calendar-days {
    border-collapse: collapse;
    margin: 0;

    thead tr{
      background: #ededf0 !important;
    }

    th{
      line-height: 1.42857143;
      padding:0;
      border-left:0;
    }

    td{
      line-height: 1.42857143;
      border-right: 1px solid #BABBC2 !important;
      border-left: 1px solid #BABBC2 !important;
    }

    thead {
      border-bottom: none;
    }

    td {
      height: 30px;
      width: 30px;
      font-size: 14px;
      padding: 2px;
      font-weight: lighter;
      border: 1px solid #BABBC2;
      text-align: center;
      vertical-align: middle;
      cursor: pointer;
      background-color: #fff;

      button {
        color: @calendar-text;
        width: auto;
        height: 18px;
        margin: 6px 5px;
      }

      &:hover {
        > span {
          background: none;
          > button {
            color: @black-hover;
            border-bottom: 1px solid @blue-hover;
          }
        }
      }
    }

    td.past {
      button {
        color: @calendar-text;
      }
    }

    td.selected {
      background: @calendar-row-selected;
      span {
        background: @calendar-row-selected;
        &:hover {
          background: @calendar-row-selected;
        }
      }
      button {
        font-weight: bold;
        color: @calendar-row-white;
        &:hover {
          border-bottom: none;
        }
        &:focus {
          border-bottom: none;
        }
      }
    }

    td.selected.current-day{
      button{
        color: @calendar-row-white;
      }
    }

    td.current-day {
      span
      {
        border:none;
      }
      button {
        border: none;
        &:hover {
          border-bottom: 1px solid @blue-hover;
        }
        &:focus {
          border-bottom: 1px solid @blue-hover;
        }
      }
    }
  }
}

.fuelux {
  .datepicker-wheels-month {
    .header {
      padding-right: 6px;
      font-size: 16px;
    }
  }

  .datepicker-wheels-year {
    .header {
      padding-right: 20px;
      font-size: 16px;
    }
  }

  .datepicker-wheels-month ul li button, .datepicker-wheels-year ul li button {
    width: 40px;
    height: 25px;
    padding: 1px;
    margin-left: 45px;
    border-right-width: 25px;
    text-align: center;
    color: #414041;
    display: block;
    font-size: 16px;
    text-decoration: none;

    &:hover {
      background: none;
      border-bottom: 1px solid @blue-hover;
    }
  }

  .datepicker-wheels-month ul li button {
    width: 32px;
  }

  .datepicker-wheels-month ul li.selected button, .datepicker-wheels-year ul li.selected button {
    font-weight: bold;
    background: @calendar-row-selected;
    color: @calendar-row-white;
  }
}

.fuelux {
  .datepicker-wheels-footer {
    border-radius: 0 0 7px 7px;

    .datepicker-wheels-back, .datepicker-wheels-select {
      margin: 10px;
      font-size: 14px;
      height: 32px;
      padding-top: 6px;
      padding-right: 19px;
      padding-left: 19px;
      border-radius: 7px;
      float: right;
      background-color: transparent;
      color: @calendar-text;

      &:hover {
        color: @calendar-text;
      }
      &:focus{
        border-bottom: none;
      }
    }

    .datepicker-wheels-back {
      float: left;
      border: 1px solid @input-border;
      &:hover {
        border: 1px solid @blue-hover;
      }
      &:focus{
        border: none;
      }
    }
  }
}

.fuelux {
  .calendar-small {
    .form-control {
      height: 26px;
    }
    .dropdown-toggle {
      padding: 1px 5px;
    }
  }
}

