@import "variables.less";
@import "../bower_components/bootstrap/less/list-group.less";

//== List group
//
//##

// Normal list (light-weight implementation)
ul.list-group {
  cursor: default;
  border-radius: 6px;
  text-shadow: 0 1px rgba(255, 255, 255, 0.65);
  border: 1px solid #b2b2b2;

  li {
    border: 1px solid white;
    margin-bottom: 0;
    font-weight: normal;
    color: @gray-nav;
    padding: 3px 10px 3px 10px;
  }
}

dl.list-group {
  cursor: default;
  border-radius: 6px;
  text-shadow: 0 1px rgba(255, 255, 255, 0.65);
  border: 1px solid #b2b2b2;

  dt {
    padding: 5px 10px;
    background-color: @n-table-row-grey;
    border: 1px solid @n-table-row-grey;
  }

  dd {
    padding: 3px 20px;
  }
}

.list-group-item {
  border: 1px solid white;
}

//list group header
.n-list-group-header {
  padding-left: 10px;
  display: block;
}

// Striped list version
.n-list-striped {
  li:nth-child(odd) {
    background-color: @n-table-row-white;
  }
  li:nth-child(even) {
    background-color: @n-table-row-grey;
  }
}

// Selectable list
// This is for making selection support into list (without it being select-input component)
.n-list-group:extend(.list-group all) {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.n-list-group-scroll {
  height: 100px;

  > .mCustomScrollBox {
    border-radius: 5px;
    padding: 0;
    width: 100%;

    > .mCSB_container {
      margin-right: 0;
    }
    &.mCS-disabled {
      background-color: white;
    }
  }
}

.n-list-group-small:extend(.n-list-group all) {
  @list-group-border-radius: 6px; //@input-border-radius;
  > li {
    padding: 1px 10px 1px 10px;
  }
  > li:first-child {
    padding: 4px 10px 2px 10px;
  }
  > li:last-child {
    padding: 2px 10px 4px 10px;
  }
  dd {
    padding: 1px 20px !important;
  }
  dd:first-child {
    padding: 4px 20px 2px 20px !important;
  }
  dd:last-child {
    padding: 2px 20px 4px 20px !important;
  }
}

.n-list-group-item:extend(.list-group-item all) {
  color: @gray-nav;
  margin-bottom: 0;
  &:hover {
    cursor: pointer;
    color: black;
    span {
      border-bottom: 1px solid @blue-hover;
      margin-bottom: -1px;
    }
  }
}

// Focus without extra outlining
li.n-list-group-item:focus {
  outline-style: none;
  box-shadow: none;
  border: 1px solid @gray-medium !important;
}

dd.n-list-group-item:focus {
  outline-style: none;
  box-shadow: none;
  border: 1px solid @gray-medium !important;
}

ul.n-list-group {
  > li:first-child.selected {
    border-top: 1px solid transparent;
  }

  > li:last-child.selected {
    border-bottom: 1px solid transparent;
  }
}
dl.n-list-group {
  > dd:first-child.selected {
    border-top: 1px solid transparent;
  }

  > dd:last-child.selected {
    border-bottom: 1px solid transparent;
  }
}

li.n-list-group-item.selected {
  font-weight: bold;
  text-shadow: none;
  background-color: @list-selected-invert;
  color: white;
  outline: none;
  border-color: transparent !important;

  &:hover {
    cursor: pointer;
    font-weight: bold;
    text-shadow: none;
    border: 1px solid @gray-medium !important;
    span {
      border-bottom: 1px solid transparent;
    }
  }
}

dd.n-list-group-item.selected {
  font-weight: bold;
  text-shadow: none;
  background-color: @list-selected-invert;
  color: white;
  outline: none;
  border-color: transparent !important;

  &:hover {
    cursor: pointer;
    font-weight: bold;
    text-shadow: none;
    border: 1px solid @gray-medium !important;
    span {
      border-bottom: 1px solid transparent;
    }
  }
}

// Disabled list
ul.disabled {
  cursor: default;
  color: @list-group-disabled-text-color;
  li.list-group-item, li.n-list-group-item {
    color: @list-group-disabled-text-color;
  }
  li.list-group-item, li.n-list-group-item {
    &:hover {
      cursor: default;
      span {
        border-bottom: none;
      }
    }
    &:focus {
      border: 1px solid transparent !important;
    }
  }
  &:before {
    color: @list-group-disabled-text-color;
  }
  &:focus {
    outline: none;
  }
}

dl.disabled {
  cursor: default;
  color: @list-group-disabled-text-color;
  dt.list-group-item, dd.n-list-group-item {
    color: @list-group-disabled-text-color;
    &:hover {
      cursor: default;
      span {
        border-bottom: none;
      }
    }
    &:focus {
      border: 1px solid transparent !important;
    }
  }
  dd.list-group-item, dd.n-list-group-item {
    color: @list-group-disabled-text-color;
    &:hover {
      cursor: default;
      span {
        border-bottom: none;
      }
    }
    &:focus {
      border: 1px solid transparent !important;
    }
  }
  &:before {
    color: @list-group-disabled-text-color;
  }
  &:focus {
    outline: none;
  }
}
