.n-flyout {
  position: absolute;

  .n-flyout-container {
    position: relative;
    display: inline-block;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    box-shadow: 3px 0 10px rgba(0, 0, 0, .3);

    .n-flyout-menu {
      margin: 0;
      padding: 0;
      text-shadow: none;

      li {
        display: block;
        height: 33px;
        padding: 0;
        border: none;

        &:nth-child(odd) {
          color: #1B1B1B;
          background-color: #efefef;
        }

        &:nth-child(even) {
          color: #000000;
          background-color: #ffffff;
        }

        &:first-child, &:last-child {
          height: 37px;
          a {
            line-height: 37px;
          }
        }

        &:first-child {
          border-top-right-radius: 5px;
        }

        &:last-child {
          border-bottom-right-radius: 5px;
        }

        a {
          display: inline-block;
          font-weight: normal;
          font-size: 16px;
          padding: 0 22px;
          color: #1b1b1b;
          line-height: 33px;
          width: 100%;

          span {
            display: inline-block;
            line-height: 1em;
            vertical-align: middle;
            border-bottom: 1px solid transparent;
            padding: 2px 0;
          }

          &:focus, &:active {
            outline: none;
            span {
              border-color: #00A1CC;
            }
          }

          &:hover{
            outline: none;
            color: #000000;
            span {
              border-color: #00A1CC;
            }
          }
        }
      }
    }
  }

  .n-flyout-open {
    position: absolute;
    outline: none;
    width: 43px;
    height: 108px;
    line-height: 108px;
    padding: 0 0 0 8px;
    background: url("../img/flyout-anchor.png") no-repeat 0 0;

    &:focus, &:active {
      .icon-list:before {
        color: #0b6daf;
      }
    }
  }
}


