@track-color: #d3d3d3;
@thumb-color: #666666;

@thumb-height: (@track-height * 2);
@thumb-width: (@track-height * 2);
@thumb-radius: (@thumb-height / 2);
@thumb-shadow-size: 1px;
@thumb-shadow-blur: 3px;
@thumb-shadow-color: #4d4d4d;
@thumb-border-width: 4px;
@thumb-border-color: #ffffff;
@thumb-border-color-hover: #00a1cc;

@track-width: 100%;
@track-height: 8px;
@track-shadow-size: 2px;
@track-shadow-blur: 1px;
@track-shadow-color: rgba(0, 0, 0, 0.5);
@track-border-width: 1px;
@track-border-color: inherit;

@track-radius: (@track-height / 2);
@contrast: 5%;

@slider-track-height: 8px;
@slider-thumb-size: (@slider-track-height * 2);
@slider-active-color: #d3d3d3;
@slider-active-shaow:0px 1px 3px 0px black inset;
@slider-track-disable-color: #ECEDED;
@slider-thumb-disable-color: #C0C0C1;

.shadow(@shadow-size,@shadow-blur,@shadow-color) {
    box-shadow: inset @shadow-size @shadow-size @shadow-blur @shadow-color, 0px 1px @shadow-size lighten(@shadow-color,5%);
}

.focusthumb() {
    //box-shadow: 0px 1px 1px #323232 ,-1px 0px 1px #323232,1px 0px 1px #323232;
    box-shadow:none;
    border-color: #323232;
    background:@thumb-border-color;
}

.track() {
    width: @track-width;
    height: @track-height;
    cursor: pointer;
    background: @track-color;
    animate: 0.2s;
}

.thunmb_chrome(){
    .shadow(@thumb-shadow-size,@thumb-shadow-blur,@thumb-shadow-color);
    border: @thumb-border-width solid @thumb-border-color;
    height: @thumb-height;
    width: @thumb-width;
    border-radius: @thumb-radius;
    background: @thumb-color;
    cursor: pointer;
    &:hover {
        box-shadow:  0 0 1px 1px #00a1cc;
    };
    &:active {
        background: @thumb-color !important;
        box-shadow: 1px 1px 3px #4D4D4D inset, 0px -1px 1px #5A5A5A ,-1px 0px 1px #5A5A5A,1px 0px 1px #5A5A5A !important;
        border-color: @track-color !important;
    }

}
.thumb() {
    .shadow(@thumb-shadow-size,@thumb-shadow-blur,@thumb-shadow-color);
    border: @thumb-border-width solid @thumb-border-color;
    height: @track-height;
    width: @track-height;
    border-radius: @thumb-radius;
    background: @thumb-color;
    cursor: pointer;
    &:hover {
        box-shadow:  0 0 1px 1px #00a1cc;
    };
    &:active {
        background: @thumb-color !important;
        box-shadow: 1px 1px 3px #4D4D4D inset, 0px -1px 1px #5A5A5A ,-1px 0px 1px #5A5A5A,1px 0px 1px #5A5A5A !important;
        border-color: @track-color !important;
    }
}

input[type=range] {
    -webkit-appearance: none;
    margin: (@thumb-height / 2) 0;
    width: @track-width;
    height:40px;
    background:rgba(255,255,255, .01);
    padding:0;
    &:focus {
        outline: none;
    }

    &::-webkit-slider-runnable-track {
        .track();
        .shadow(@track-shadow-size,@track-shadow-blur,@track-shadow-color);
        //background: @track-color;
        border-radius: @track-radius;
        border: @track-border-width solid @track-border-color;
    }

    &::-webkit-slider-thumb {
        .thunmb_chrome();
        -webkit-appearance: none;
        margin-top: (0 - (((@thumb-height / 2) - @track-height) + (@track-height / 2)));

    }

    &:disabled::-webkit-slider-runnable-track {
        background: @slider-track-disable-color;
    }

    &:disabled::-webkit-slider-thumb {
        .thunmb_chrome();
        -webkit-appearance: none;
        //margin-top: 0 - ((@thumb-height / 2) - @track-height + (@track-height / 2));
        margin-top: (0 - (((@thumb-height / 2) - @track-height) + (@track-height / 2)));
        border-color: @slider-track-disable-color;
        background:@slider-thumb-disable-color;
        &:hover {
            .shadow(@thumb-shadow-size,@thumb-shadow-blur,@thumb-shadow-color);
        };
    }

    &:focus::-webkit-slider-thumb {
        .focusthumb();
    }

    &:focus::-webkit-slider-runnable-track {
        //background: lighten(@track-color, @contrast);
        background: @track-color;
    }


    &::-moz-focus-outer{
        border: 0;
    }

    &::-moz-range-track {
        .track();
        .shadow(@track-shadow-size,@track-shadow-blur,@track-shadow-color);
        //background: @track-color;
        border-radius: @track-radius;
        border: @track-border-width solid @track-border-color;
    }

    &::-moz-range-thumb {
        .thumb();
    }

    &:disabled::-moz-range-track {
        background: @slider-track-disable-color;
    }

    &:disabled::-moz-range-thumb {
        .thumb();
        border-color: @slider-track-disable-color;
        background:@slider-thumb-disable-color;
        &:hover {
            .shadow(@thumb-shadow-size,@thumb-shadow-blur,@thumb-shadow-color);
        };
        &:active {
            background:@slider-thumb-disable-color;
            .shadow(@thumb-shadow-size,@thumb-shadow-blur,@thumb-shadow-color);
            border-color: @slider-track-disable-color;
        }
    }

    &:focus::-moz-range-thumb{
        .focusthumb();
    }

    &:focus::-moz-range-track{
        background: @track-color;
    }


    &::-ms-track {
        .track();
        background: transparent;
        border-color: transparent;
        border-width: @thumb-width 0;
        color: transparent;

    }

    &::-ms-fill-lower {
        background: @track-color;
        border: @track-border-width solid @track-border-color;
        border-bottom-left-radius:@track-radius;
        border-top-left-radius: @track-radius;
        .shadow(@track-shadow-size,@track-shadow-blur,@track-shadow-color);
    }
    &::-ms-fill-upper {
        background: @track-color;
        border: @track-border-width solid @track-border-color;
        border-bottom-right-radius:@track-radius;
        border-top-right-radius: @track-radius;
        .shadow(@track-shadow-size,@track-shadow-blur,@track-shadow-color);
    }
    &::-ms-thumb {
        .thumb();
        margin-left:1px;
        margin-right:1px;

    }

    &:disabled::-ms-thumb{
        .thumb();
        border-color: @slider-track-disable-color;
        background:@slider-thumb-disable-color;
        &:hover {
            .shadow(@thumb-shadow-size,@thumb-shadow-blur,@thumb-shadow-color);
        };
        &:active {
            background:@slider-thumb-disable-color;
            .shadow(@thumb-shadow-size,@thumb-shadow-blur,@thumb-shadow-color);
            border-color: @slider-track-disable-color;
        }
    }

    &:disabled ::-ms-track {
        background: @slider-track-disable-color;
    }

    &:disabled::-ms-fill-lower {
        background: @slider-track-disable-color;
    }

    &:disabled::-ms-fill-upper {
        background: @slider-track-disable-color;
    }

    &:focus::-ms-thumb{
        .focusthumb();
    }
    &:focus::-ms-fill-lower {
        background: @track-color;
    }
    &:focus::-ms-fill-upper {
        background: @track-color;
    }
}


