@import "../bower_components/bootstrap/less/navbar.less";

// Note impacting components that contain banner specific settings: dropdown.less

@n-banner-1st-row-left-gutter: 0px;
@n-banner-1st-row-right-gutter: 0px;
@n-banner-2nd-row-left-gutter: 0px;
@n-banner-2nd-row-right-gutter: 0px;

@n-banner-height: 118px;

@n-banner-1st-gradient-white-height: 78px;
@n-banner-1st-gradient-white-height-condensed: 51px;
@n-banner-1st-gradient-white-start: #e5e5ea;
@n-banner-1st-gradient-white-end: #fff;
@n-banner-1st-gradient-blue-height: 60px;
@n-banner-1st-gradient-blue-height-condensed: 45px;
@n-banner-1st-gradient-blue-start: #1852aa;
@n-banner-1st-gradient-blue-end: #0b2a67;
@n-banner-2nd-gradient-blue-height: 40px;
@n-banner-2nd-gradient-blue-start: #1a64c1;
@n-banner-2nd-gradient-blue-end: #0e3277;
@n-banner-2nd-gradient-gray-height: 40px;
@n-banner-2nd-gradient-gray-start: #cccdd5;
@n-banner-2nd-gradient-gray-end: #c4c5cd;
@n-banner-2nd-icon-color: #A8BBC0;
@n-banner-2nd-icon-text-shadow: 0 -1px rgba(0, 0, 0, 1);
@n-banner-min-width: 1072px;
@n-banner-dropdown-curve-gradient-end: #aeb2b4;
//#a9adb0;
@n-banner-dropdown-curve-gradient-start: #a8acaf;
//#9ea3a6;
@n-banner-breakpoint: 666px;
@n-banner-3rd-filler-gray-top: 116px;
@n-banner-3rd-filler-gray-top-condensed: 91px;

.n-banner {
    position: relative;
    min-height: @n-banner-height;
    border-top: 1px solid #fff;

    padding-top: 16px;

    // Prevent floats from breaking the navbar
    &:extend(.clearfix all);

    //@media (max-width: @n-banner-min-width) {
    //    min-width: @n-banner-min-width;
    //}
    width: 100%;
    min-width: @n-banner-min-width;
    @media (max-width: (@grid-float-breakpoint - 2px)) {
        width: 100%;
        min-width: 100%;
    }
    @media (min-width: @grid-float-breakpoint) {
        border-radius: @navbar-border-radius;
    }

}

.n-banner-header {
    &:extend(.clearfix all);

    @media (min-width: @grid-float-breakpoint) {
        float: left;
    }
}

.n-banner-collapse {
    overflow-x: visible;
    padding-right: @navbar-padding-horizontal;
    padding-left: @navbar-padding-horizontal;
    border-top: 1px solid transparent;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, .1);
    &:extend(.clearfix all);
    -webkit-overflow-scrolling: touch;

    &.in {
        overflow-y: auto;
    }

    @media (max-width: @grid-float-breakpoint-max) {
        overflow-y: visible !important;
        overflow-x: visible;
        //height:420px;
        margin-right: auto !important;
        margin-left: auto !important;
    }

    @media (min-width: @grid-float-breakpoint) {
        width: auto;
        border-top: 0;
        box-shadow: none;

        &.collapse {
            display: block !important;
            visibility: visible !important;
            height: auto !important;
            padding-bottom: 0; // Override default setting
            overflow: visible !important; //
        }

        &.in {
            overflow-y: visible;
        }

    }
}

.container,
.container-fluid {
    > .n-banner-header,
    > .n-banner-collapse {
        margin-right: -@navbar-padding-horizontal;
        margin-left: -@navbar-padding-horizontal;

        @media (min-width: @grid-float-breakpoint) {
            margin-right: 0;
            margin-left: 0;
        }
    }
}

