// Brand/project name

.n-banner-brand {
    float: left;
    //padding: @navbar-padding-vertical @navbar-padding-horizontal;
    padding: 0 15px;
    font-size: @font-size-large;
    //line-height: @line-height-computed;
    line-height: 61px;
    //height: @navbar-height;
    height: @n-banner-1st-gradient-blue-height;

    color: #fff;
    font-weight: bold;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.45);

    &:active,
    &:hover,
    &:focus {
        border: none;
        color: #fff;
        outline: 0;
        text-decoration: none;
    }

    > img {
        display: block;
    }

    > span {
        color: #ccc;
    }

    > h1 {
        margin-top: 10px;
        margin-bottom: 17px;
    }

    @media (min-width: @grid-float-breakpoint) {
        .n-banner-brand > .container &,
        .n-banner-brand > .container-fluid & {
            margin-left: -@navbar-padding-horizontal;
        }
    }
}

.n-banner-toggle {
    position: relative;
    float: right;
    margin-right: @navbar-padding-horizontal;

    padding: 9px 10px;
    .navbar-vertical-align(34px);
    background-color: transparent;
    background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
    border: 1px solid transparent;
    border-radius: @border-radius-base;

    // We remove the `outline` here, but later compensate by attaching `:hover`
    // styles to `:focus`.
    &:focus {
        outline: 0;
    }

    // Bars
    .icon-bar {
        display: block;
        width: 22px;
        height: 2px;
        border-radius: 1px;
        background-color: white;
    }
    .icon-bar + .icon-bar {
        margin-top: 4px;
    }

    margin-top: 12px;
    margin-bottom: 12px;

    @media (min-width: @grid-float-breakpoint) {
        display: none;
    }
}

@media (min-width: @grid-float-breakpoint) {
    .n-banner-left {
        .pull-left();
    }

    .n-banner-right {
        .pull-right();
        margin-right: -@navbar-padding-horizontal;

        ~ .n-banner-right {
            margin-right: 0;
        }
    }
}

// Navbar nav links
//
// Builds on top of the `.nav` components with its own modifier class to make
// the nav the full height of the horizontal nav (above 768px).

.n-banner-nav {
    margin: (@navbar-padding-vertical / 2) -@navbar-padding-horizontal;
    background: linear-gradient(to bottom, #d6d7d8, #9ca1a4);
    height: @n-banner-1st-gradient-blue-height;
    padding-top: 6px;

    > li > a {
        line-height: @line-height-computed;
    }

    @media (max-width: @grid-float-breakpoint-max) {
        // Dropdowns get custom display when collapsed
        // .open .dropdown-menu {
        position: static;
        float: none;
        width: auto;
        margin-top: 0;
        margin-bottom: 0;
        background-color: transparent;
        border: 0;
        box-shadow: none;
        > li > a,
        .dropdown-header {
            padding: 5px 15px 5px 25px;
        }

        > li > a {
            line-height: @line-height-computed;
            &:hover,
            &:focus {
                background-image: none;
            }
        }

        // }
    }

    // Uncollapse the nav
    @media (min-width: @grid-float-breakpoint) {

        float: left;
        margin: 0;

        > li {
            float: left;
            margin: 0 8px;
        }
    }
}

.n-banner-1st > ul > li {
    @media (max-width: @grid-float-breakpoint-max) {

        margin: 0 8px;
        float: left;

    }
    margin: 0 3px;

    > p {
        padding: 15px 0;
    }

    > a {
        color: #333;
        font-size: 14px;
        font-weight: bold;
        margin: 0;
        text-decoration: none;
        text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
        padding-left: 3px;
        padding-right: 3px;

        &:active, &:focus, &:hover {
            background: transparent;
            border: none;
            outline: 0;
            text-shadow: 0 0 3px rgba(255, 255, 255, 1);
        }
    }
}

