    height: @n-banner-1st-gradient-white-height;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: -1;
}

.n-banner-1st-filler-blue {
    #gradient > .vertical(@n-banner-1st-gradient-blue-start, @n-banner-1st-gradient-blue-end);
    height: @n-banner-1st-gradient-blue-height;
    left: 0;
    position: absolute;
    top: 16px;
    width: 100%;
    /*@media (min-width: @screen-sm-min) {
        width: calc(50% + @container-sm-no-gutter/2);
    }
    @media (min-width: @screen-md-min) {
        width: calc(50% + @container-md-no-gutter/2);
    }
    @media (min-width: @screen-lg-min) {
        width: calc(50% +  @container-lg-no-gutter/2);
    }*/
    z-index: -1;
}

.n-banner-2nd {
    li > a {

        padding-top: 10px;
        padding-bottom: 10px;
    }
    height: @n-banner-2nd-gradient-blue-height;
    .n-banner-dropdown-toggle {
        #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end);
    }

    .n-banner-tabs {
        @media (max-width: @grid-float-breakpoint-max) {
            padding-left: 0px;
            height: auto;
        }
        > li {
            & > a {
                @media (max-width: @grid-float-breakpoint-max) {
                    border-radius: 0;
                }
                padding-left: 11px;
                padding-right: 11px;
            }
        }
    }
}

.n-banner-2nd-filler-blue {
    #gradient > .vertical(@n-banner-2nd-gradient-blue-start, @n-banner-2nd-gradient-blue-end);
    height: @n-banner-2nd-gradient-blue-height;
    left: 0;
    position: absolute;
    top: 76px;
    width: 100%;
    z-index: 0;
}

.n-banner-2nd-filler-gray {
    #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end);
    height: @n-banner-2nd-gradient-gray-height;
    left: 0;
    position: absolute;
    top: 76px;
    z-index: -1;
    width: 100%;
}

.n-banner-3rd-filler-gray {
    display: none;
    background: @n-banner-tab-background-active;
    height: @n-banner-2nd-gradient-gray-height;
    border-bottom: 1px solid #b2b2b2;
    left: 0;
    position: absolute;
    top: @n-banner-3rd-filler-gray-top;
    z-index: 0;
    width: 100%;
}

.n-banner-3rd {
    ul {
        list-style-type: none;

        li {
            float: left;
            &:hover > a > span {
                border-bottom: 1px solid @blue-hover;
            }
            &.active > a > span {
                color: #000;
            }
            a {
                display: block;
                float: left;
                cursor: pointer;
                color: @n-banner-tab-text-color;
                font-weight: normal;
                text-shadow: 0 1px rgba(255, 255, 255, 0.65);
                padding-left: 37px;
                padding-right: 8px;
                :hover {
                    text-decoration: none;
                    color: #000;
                }

                span {
                    float: left;
                    line-height: 20px;
                }
            }
        }
    }
}

.n_banner_3rd_subItem {
    display: none;
    float: right;
    height: 40px;
    > li {
        > a {
            outline-style: none;
        }
        > a:hover, > a:active {
            background: transparent;
        }
        > a:focus {
            background: transparent;
            > span {
                border-bottom: 1px solid @blue-hover;
            }
        }
    }
}

.n-banner-logo-img {
    background: linear-gradient(to bottom, #e5e5ea -16px, #fff);
    height: @n-banner-1st-gradient-blue-height;
    padding-top: 17px;
    width: 152px;
    padding-left: 22px;
}

// DIALOG BANNER
.n-banner-dlg-filler-gray {
    #gradient > .vertical(@n-banner-dlg-gradient-gray-start, @n-banner-dlg-gradient-gray-end);
    height: @n-banner-dlg-gradient-gray-height;
    left: 0;
    position: absolute;
    top: 10px;
    z-index: 1;
}

// "Wave" transitions

.n-banner-1st-blue-to-gray {
    height: @n-banner-1st-gradient-blue-height;
    min-width: 100px;
    padding: 0;
    position: relative;
    width: 100px;

    > .blue-mask {
        background: linear-gradient(to bottom, #1852aa -30px, #0b2a67);
        bottom: 0;
        height: 50%;
        left: 0;
        position: absolute;
        width: 100%;
    }
    > .gray-mask {
        background: linear-gradient(to bottom, #d6d7d8, #9ca1a4 61px);
        height: 50%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
    > .blue-corner {
        background: linear-gradient(to bottom, #1852aa, #0b2a67);
        border-radius: 0 100px 0 0;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +1;
        //glitch fix in ie
        left: -1px;
        width: calc(50% + 2px);

    }
    > .gray-corner {
        background: linear-gradient(to bottom, #d6d7d8, #9ca1a4);
        border-radius: 0 0 0 100px;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +1;
        //glitch fix in ie
        width: 63px;
        right: -1px;
    }
}

.n-banner-1st-gray-to-white {
    height: @n-banner-1st-gradient-blue-height;
    min-width: 100px;
    padding: 0;
    position: relative;
    width: 100px;

    > .gray-mask {
        background: linear-gradient(to bottom, #d6d7d8 -30px, #9ca1a4);
        bottom: 0;
        height: 50%;
        left: 0;
        position: absolute;
        width: 100%;
    }
    > .white-mask {
        background: linear-gradient(to bottom, #e5e5ea -16px, #ffffff 78px);
        height: 50%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
    > .gray-corner {
        background: linear-gradient(to bottom, #d6d7d8, #9ca1a4);
        border-radius: 0 100px 0 0;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +1;
        //glitch fix in ie
        width: calc(50% + 2px);
        left: -1px;
    }
    > .white-corner {
        background: linear-gradient(to bottom, #e5e5ea -16px, #ffffff);
        border-radius: 0 0 0 100px;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +1;
        //glitch fix in ie
        width: 63px;
        right: -1px;
    }
}

.n-banner-2nd-gray-to-blue {
    display: inline-block;
    height: 40px;
    min-width: 100px;
    padding: 0;
    position: relative;
    width: 100px;

    > .gray-mask {
        background: linear-gradient(to bottom, #CCCDD5 -15px, #C4C5CD);
        bottom: 0;
        height: 50%;
        left: 0;
        position: absolute;
        width: 100%;
        z-index: +1;
    }
    > .blue-mask {
        background: linear-gradient(to bottom, #1A64C1, #0E3277 30px);
        height: 50%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: +1;
    }
    > .gray-corner {
        background: linear-gradient(to bottom, #CCCDD5, #C4C5CD);
        border-radius: 0 100px 0 0;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +1;
        //glitch fix in ie
        width: calc(50% + 2px);
        left: -1px;
    }
    > .blue-corner {
        background: linear-gradient(to bottom, #1A64C1, #0E3277);
        border-radius: 0 0 0 100px;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +99;
        //glitch fix in ie
        width: 62px;
        right: -1px;
    }

    &.active, &.active:hover {
        > .gray-mask {
            background: @n-banner-tab-background-active;
        }
        > .gray-corner {
            background: @n-banner-tab-background-active;
            border-top: 1px solid @nav-tabs-border-color;
        }
    }
}

.n-banner-overflow-control {
    width: 0;
    .n-banner-dropdown-toggle {
        display: block;
        height: @n-banner-2nd-gradient-gray-height;
        box-shadow: 0 0 8px 0 #000;
        border-left: 1px solid;
        .icon-dropdown-menu {
            &:before {
                color: @dropdown-menu-color;
            }
            left: 13px;
            top: 13px;
            z-index: +100;
        }
        .overflow-toggle-area {
            &:before {
                content: '';
                height: @n-banner-2nd-gradient-gray-height;
                width: 59px;
                position: absolute;
                top: 0;
                left: -1px;
                background: linear-gradient(to bottom, #d6d7d8, #9ca1a4);
                border-radius: 0 100px 0 0;
                z-index: 99;
            }
            &:after {
                content: '';
                height: 21px;
                width: 83px;
                position: absolute;
                left: -1px;
                bottom: 0;
                background: linear-gradient(to bottom, #b9bcbe, #9ca1a4);
                z-index: 99;
            }
        }
        .overflow-toggle-area-cover {
            &:after {
                content: '';
                width: 55px;
                height: @n-banner-2nd-gradient-blue-height;
                position: absolute;
                top: 0;
                left: 46px;
                border-radius: 0 0 0 100px;
                #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end);
                z-index: 100;
            }
            display: none;
        }
        &:hover[aria-expanded=false] {
            .overflow-toggle-area {
                &:before {
                    border-left: 2px solid @blue-hover;
                    border-top: 0px @blue-hover;
                }
                &:after {
                    border-left: 2px solid @blue-hover;
                    border-bottom: 2px solid @blue-hover;
                }
            }
        }
        &:focus {
            outline: 5px;
        }

        &:focus, &:hover,
        &[aria-expanded=true] {
            .overflow-toggle-area {
                &:before {
                    background: @dropdown-menu-background;
                }
                &:after {
                    background: @dropdown-menu-background;
                }
            }
        }
        &[aria-expanded=true] > .overflow-toggle-area {
            &:before {
                border-left: 1px solid rgba(0, 0, 0, 0.15)
            }
            &:after {
                left: 1px;
            }
        }
    }
    .dropdown-menu {
        border-top: none;
        margin-top: 0px;
        border: 1px rgba(0, 0, 0, 0.15);
        > li > a {
            &:hover {
                background-color: white;
                > span {
                    border-bottom-color: @blue-hover;
                }
            }

            &:focus {
                background-color: #0B6DAF;
                font-size: 14px;
                font-family: @font-family-sans-serif;
                color: #FFFFFF;
                > span {
                    border-bottom-color: @blue-selection;
                }
            }

            > span {
                padding-bottom: 1px;
                border-bottom: 1px solid transparent;
            }
        }
    }
}

@n-banner-tab-text-color: #333;
@n-banner-tab-background-active: #d8dae0;
@n-banner-tab-background-hover: #D0D1D8;
@n-banner-tab-border-color-hover: #c3c3c3;
.n-banner-corner-on-tab(@border-color, @background-color) {
    outline: 0;
    & > span {
        color: black;
        text-shadow: none;
        &:before,
        &:after {
            content: '';
            position: absolute;
            width: @nav-tabs-border-radius;
            height: @nav-tabs-border-radius;
            bottom: 0;
            border-bottom: 1px solid @border-color;
            #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end, -85%);
            z-index: 3;
            @media (max-width: @grid-float-breakpoint-max) {
                display: none;
            }
        }
        &:before {
            margin-left: -@nav-tabs-border-radius;
            border-right: 1px solid @border-color;
            left: 0;
            border-radius: 0 0 @nav-tabs-border-radius 0;
        }
        &:after {
            margin-right: -@nav-tabs-border-radius;
            border-left: 1px solid @border-color;
            right: 0;
            border-radius: 0 0 0 @nav-tabs-border-radius;
        }
    }
    &:after {
        content: '';
        position: absolute;
        width: calc(100% + 14px);
        height: @nav-tabs-border-radius;
        background: @background-color;
        bottom: 0;
        left: -7px;
        z-index: 2;
        @media (max-width: @grid-float-breakpoint-max) {
            display: none;
        }
    }
}

.n-banner-corner-on-drop-down-tab(@border-color, @background-color) {
    .n-banner-corner-on-tab(@border-color, @background-color);
    &:after {
        left: 0px;
        width: calc(100% + 7px);
    }
    & > span:before {
        visibility: hidden;
    }
}

.n-banner-tabs {
    position: relative;
    margin-left: -15px;
    padding-left: 15px;
    #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end);
    height: @n-banner-2nd-gradient-blue-height;
    padding-top: 0;

    > li {
        height: 100%;
        & > a {
            height: 100%;
            line-height: 40px;
            color: @n-banner-tab-text-color;
            font-size: 14px;
            font-weight: normal;
            line-height: calc(@n-banner-2nd-gradient-blue-height - 1px);
            padding: 0 15px;
            text-shadow: 0 1px rgba(255, 255, 255, 0.65);
            border: 1px solid transparent;
            border-bottom: none;
            border-radius: 7px 7px 0 0;

        }

        &.active > a, &.active:hover > a {
            background: @n-banner-tab-background-active;
            text-shadow: none !important;
            border: 1px solid;
            border-bottom: none;
            border-color: @nav-tabs-border-color;
            .n-banner-corner-on-tab(@nav-tabs-border-color, @n-banner-tab-background-active);
            > span {
                color: #333333;
            }
        }

        &:hover {
            > a {
                background: @n-banner-tab-background-hover;
                border-radius: @nav-tabs-border-radius @nav-tabs-border-radius 0 0;
                border: 1px solid;
                border-bottom: none;
                border-color: @n-banner-tab-border-color-hover;
                .n-banner-corner-on-tab(@n-banner-tab-border-color-hover, @n-banner-tab-background-hover);

                > span {
                    color: #000000;
                    border-bottom: 1px solid @blue-hover;
                    margin-bottom: 0px;
                }
                a.active {
                    background: @n-banner-tab-background-active;
                }
            }
        }
    }

    @media (max-width: @grid-float-breakpoint-max) {
        > li {
            background-color: #C9CAD2;
            height: 100%;
            & > a {
                height: 100%;
                line-height: 40px;
                color: @n-banner-tab-text-color;
                font-size: 14px;
                font-weight: normal;
                line-height: calc(@n-banner-2nd-gradient-blue-height - 1px);
                padding: 0 15px;
                text-shadow: 0 1px rgba(255, 255, 255, 0.65);
                border: 1px solid transparent;
                border-bottom: none;
                border-radius: 0;
            }

            &.active > a, &.active:hover > a {
                background: @n-banner-tab-background-active;
                text-shadow: none !important;
                border: 1px solid;
                border-bottom: none;
                border-color: @nav-tabs-border-color;
                .n-banner-corner-on-tab(@nav-tabs-border-color, @n-banner-tab-background-active);
                > span {
                    color: #333333;
                }
            }

            &:hover {
                > a {
                    background: @n-banner-tab-background-hover;
                    border-radius: 0;
                    border: 1px solid;
                    border-bottom: none;
                    border-color: @n-banner-tab-border-color-hover;
                    .n-banner-corner-on-tab(@n-banner-tab-border-color-hover, @n-banner-tab-background-hover);
                    > span {
                        color: #000000;
                        border-bottom: 1px solid @blue-hover;
                    }
                    a.active {
                        background: @n-banner-tab-background-active;
                    }
                }
            }
        }
    }

    > li.dropdown {
        .dropdown-menu {
            border-radius: 0 5px 5px 5px;
            margin-top: -1px;
            border-color: @input-border;
        }
        &.open > a {
            background: @dropdown-menu-background;
            box-shadow: 0 1px 3px 0 #a7a7a7;
            border: 1px solid;
            border-bottom: none;
            border-color: @nav-tabs-border-color;
            .n-banner-corner-on-drop-down-tab(@nav-tabs-border-color, @dropdown-menu-background);
            &:after {
                height: 8px;
                margin-bottom: -3px;
                z-index: 1001;
            }
            > span:after {
                z-index: 1002;
            }
            > span:not(:hover) {
                border-bottom: none;
            }
        }
    }
    // right most tab will highlight part of the curve when focus / hover

    > li.rightmost-tab {
        margin-right: 0;
        > a {
            border-radius: 7px 0 0 0;
            padding-right: 0;

            &:hover {
                &:before {
                    position: absolute;
                    content: '';
                    bottom: 0;
                    right: 0;
                    z-index: 3;
                    margin-right: -100px;
                    background: @n-banner-tab-background-hover;
                    width: 100px;
                    height: 20px;
                }
                > span:after {
                    position: absolute;
                    content: '';
                    height: 39px;
                    width: 50px;
                    background: @n-banner-tab-background-hover;
                    margin-right: -50px;
                    border: none;
                    border-radius: 0 100px 0 0;
                }
            }

            &:focus {
                &:before {
                    position: absolute;
                    content: '';
                    bottom: 0;
                    right: 0;
                    z-index: 3;
                    margin-right: -100px;
                    background: @n-banner-tab-background-active;
                    width: 100px;
                    height: 20px;
                }
                > span:after {
                    position: absolute;
                    content: '';
                    height: 39px;
                    width: 50px;
                    background: @n-banner-tab-background-active;
                    margin-right: -50px;
                    border: none;
                    border-radius: 0 100px 0 0;
                }
            }
        }
        &.active > a {
            > span:after {
                background: @n-banner-tab-background-active;
                border-color: @n-banner-tab-background-active;
            }

            &:hover:before {
                background: @n-banner-tab-background-active;
            }
        }
    }
}

.n-banner-dropdown-toggle {
    border: none;
    box-shadow: none;
    span.caret {
        border-bottom: none !important;
        padding-bottom: 4px;
    }
    // +++ Keyboard focusing or mouse opened - keep this location before hovering
    &:focus:not(.disabled):not([aria-expanded=true]) {
        background: @n-banner-tab-background-active;
    }
    // +++ Hovering but list not open
    &:hover[aria-expanded=false], &:hover:not([aria-expanded]) {
        color: black;
        border: 1px solid;
        border-bottom: none;
        border-color: @n-banner-tab-border-color-hover;
        box-shadow: none !important;
        background: @n-banner-tab-background-hover;
    }
    // +++ List opened
    &[aria-expanded=true] {
        color: black;
        border-radius: 7px 7px 0 0;
        background: @dropdown-menu-background !important;
    }
    &:active:not([aria-expanded]), &:active[aria-expanded=false] {
        #gradient > .vertical(@dropdown-banner-background-hover, @dropdown-banner-background-hover);
        &:hover {
            border-color: @nav-tabs-border-color;
            box-shadow: none !important;
        }
    }
    i.icon.icon-arrow {
        vertical-align: middle;
    }
}

