.n-banner-1st {
    li > a {
        padding-top: 15px;
    }
    // Tune the dropdown upper part to be a bit smaller
    a.n-banner-dropdown-toggle[aria-expanded=true] {
        margin-top: 3px;
        padding-top: 12px;
        height: 40px;
    }
    a.n-banner-dropdown-toggle[aria-expanded=false] {
        margin-top: 0px;
        height: 45px;
    }
    .dropdown.a {
        background-color: transparent;
    }

    .n-banner-dropdown-toggle[aria-expanded=false] {
        &:hover {
            background-color: transparent;
            border: none;
        }
        &:focus {
            background-color: transparent;
        }
    }
    // Rounding the border corners
    .n-banner-dropdown-toggle[aria-expanded=true] {
        & > span {
            &:before {
                content: '';
                height: 7px;
                width: 7px;
                background-color: @dropdown-menu-background;
                position: absolute;
                bottom: 0;
                right: 0;
                margin-right: -7px;
                z-index: 1;

            }

            &:after {
                content: '';
                height: 7px;
                width: 7px;
                #gradient > .vertical(@n-banner-dropdown-curve-gradient-end, @n-banner-dropdown-curve-gradient-start);
                position: absolute;
                bottom: 0;
                right: 0;
                margin-right: -7px;
                border-radius: 0 0 0 7px;
                z-index: 2;
            }
        }
    }

    .dropdown-menu {
        border-radius: 0px 7px 7px 7px;
        border: none;
        > li > a {
            height: 26px;
            line-height: 26px;
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    .n-search {
        width: 178px;
        //@media (max-width: @grid-float-breakpoint-max){
        //    margin-top: 28px;
        //}
        margin-top: 15px;
        margin-right: -10px;
        z-index: 1;

        @media (max-width: @grid-float-breakpoint-max) {
            margin-top: 5px;
            margin-bottom: 5px;
            margin-left: -6px;
        }

        .n-search-icon .icon:before {
            color: #a8bbc0;
        }

        .n-search-input {
            background-color: #1249a8;
            color: #ffffff;
            border-color: transparent;
            box-shadow: 1px 1px 1px #000 inset;

            &:focus, &:hover {
                box-shadow: none;
                border-color: @blue-hover;
            }

        }
    }
}

// This is not in use at the moment in demo, but if someone uses navbar, the styles are ok
@media (min-width: 768px) {

    .navbar.nav-secondary-vertical {
        max-width: 300px;
        margin-right: 0;
        margin-left: 0;
        padding: 0;
        border-radius: @navbar-border-radius;
        border: 1px solid @navbar-border-color;
        background: @navbar-background-color;
    }

    .nav-secondary-vertical > .navbar-nav,
    .nav-secondary-vertical > .navbar-nav > li,
    .nav-secondary-vertical > .navbar-left,
    .nav-secondary-vertical > .navbar-right,
    .nav-secondary-vertical > .navbar-header {
        float: none !important;
    }

    // Get the link font styles corrected
    .nav-secondary-vertical > .navbar-nav {
        li {
            > a {
                //color: red !important;
                color: @text-color;
                font-weight: normal;
                &:hover, &:focus {
                    background-color: white;
                    color: black;
                    // +++ Get rid of outline for Moz
                    outline: none;
                    > span {
                        border-bottom: 1px solid @blue-hover;
                    }
                }
            }

            // Not using &:active > a as that changes color already at mouse click
            &.active > a {
                background-color: @blue-selection;
                color: white;
            }
            // Correct the rounding for first and last item in the menu
            &:first-child {
                > a:hover, > a:focus, &:active > a, &.active > a {
                    border-top-right-radius: @navbar-border-radius;
                    border-top-left-radius: @navbar-border-radius;
                }
            }
            &:last-child {
                > a:hover, > a:focus, &:active > a, &.active > a {
                    border-bottom-right-radius: @navbar-border-radius;
                    border-bottom-left-radius: @navbar-border-radius;
                }
            }
        }
    }

}

