@import "inputfield.less";
@import "variables.less";

@n-search-height: 30px;
@n-search-width: 200px;
@n-search-input-corner-radius: 15px;
@n-search-highlight-border: inset 0 0 0 1px lighten(@input-border-hover, 30%);
@n-search-border-up-round: @n-search-input-corner-radius @n-search-input-corner-radius 0 0;
@n-search-border-left-round: @n-search-input-corner-radius 0 0 @n-search-input-corner-radius;
@n-search-border-right-round: 0 @n-search-input-corner-radius @n-search-input-corner-radius 0;
@n-search-border-up-left-round: @n-search-input-corner-radius 0 0 0;
@n-search-border-up-right-round: 0 @n-search-input-corner-radius 0 0;

/* mix-in definition*/
.highlight-border-on-hover {
  background: white;
  border-color: @input-border-hover;
  box-shadow: @n-search-highlight-border;
}

.n-search {
  position: relative;
  width: @n-search-width;

  &-icon {
    cursor: default;
    left: 0;
    padding-left: 11px;
    position: absolute;
    top: 0;
  }

  &-control-icon,
  &-button {
    border-radius: @n-search-border-right-round;
    cursor: pointer;
    height: @n-search-height;
    margin: 0;
    padding: 0 10px 0 9px;
    position: absolute;
    right: 0;
    top: 0;
  }

  &-button {
    box-shadow: none;

    &:focus {
      box-shadow: none;
    }
    &:hover:not(:focus) {
      box-shadow: @n-search-highlight-border;
    }
  }

  &-input {
    border-radius: @n-search-input-corner-radius;
    height: @n-search-height;
    padding: 0 10px 0 38px;
    width: inherit;
  }

  .dropdown-toggle {
    background: transparent;
    border: 1px solid transparent;
    border-radius: @n-search-border-left-round;
    box-shadow: none;
    height: @n-search-height;
    cursor: pointer;

    &:hover {
      .highlight-border-on-hover;
    }

    &:focus:not(:hover){
      background-image:none !important;
      border: 1px solid @focus-border-style;
    }

    &[aria-expanded=true] {
      background: transparent !important;
      border: 1px solid transparent !important;
      border-radius: @n-search-border-left-round !important; //TODO: because there is !important in .dropdown-toggle[aria-expanded=true]. unwanted, must get rid of
    }
  }

  .dropdown-menu {
    background-color: @dropdown-menu-background;
    width: inherit;
    * {
      background-color: transparent;
    }
  }

  .icon:not(.icon-close) {
    font-size: 12px;
    line-height: @n-search-height;
    .user-select(none);
    & + .icon {
      margin-left: -3px;
    }
  }

  &.open {
    .n-search-input {
      border-radius: @n-search-border-up-round;
    }

    .n-search-control-icon,
    .n-search-button {
      border-radius: @n-search-border-up-right-round;
    }

    .dropdown-toggle {
      border-radius: @n-search-border-up-left-round !important;
      &:hover {
        background-color: transparent;
        border-color: transparent;
        box-shadow: none;
      }
    }
  }
  a:focus {
    outline:none;
  }
}

.n-search-action .n-search-input
{
    padding-right: 67px;
}

.n-search-clearable .n-search-input {
  padding-right: 35px;
}

.n-search-clearable {
  .n-search-control-icon {
    line-height: 33px;
    .icon-close {
      border: 1px solid transparent;
      border-radius: 5px;
    }
    &:focus > .icon-close {
      border-color: @focus-border-style;
    }
    &:hover > .icon-close {
      border-color: @input-border-hover;
    }
  }
}

.n-search-disable {
  .n-search-icon {
    position: absolute;
    top: 0px;
    left: 0px;
    padding-left: 11px;
    cursor: default;

    .icon-arrow:before {
      color: #afafaf !important;
    }

    .icon-search:before {
      color: #afafaf !important;
    }
  }

  .n-search-control-icon {
    line-height: 33px;
    .icon-close {
      border: 1px solid transparent;
      border-radius: 5px;
      &:before {
        color: #afafaf !important;
      }
    }
    &:focus > .icon-close {
      border-color: #F3F3F3;
    }
    &:hover > .icon-close {
      border-color: #F3F3F3;
    }
    &[disabled] {
      border-bottom: 1px solid @input-border-disabled;
    }
  }
}
