.n-textarea {
    overflow: hidden;
    position: relative;
    background: #FFFFFF;
    border-radius: 7px;
    float: left;
    height: 100%;
    width: 100%;
    border: 1px solid #b2b2b2;
    font: inherit;
    margin: 0;
    color: inherit;
    padding: 6px 11px;
    resize: none;
    outline: none;
    &:hover {
        border: 1px solid #00a1cc;
        -webkit-box-shadow: 0 1px 0 #ffffff, inset 0 0 0 1px #66dfff;
        box-shadow: 0 1px 0 #ffffff, inset 0 0 0 1px #66dfff;
    }
    &:focus {
        border-radius: 7px;
        border: 1px solid @input-border-focus;
        box-shadow: none;
    }
    &[disabled] {
        background: #f3f3f3;
        user-select: none;
        color: #aaaaaa;
        box-shadow: none;
        border: 1px solid #cccccc;
        box-shadow: none;
        &:hover {
            border: 1px solid #cccccc;
            box-shadow: none;
        }
    }
    &[readonly] {
        background: #f3f3f3;
        border: 1px solid #cccccc;
        box-shadow: none;
        &:hover {
            border: 1px solid #cccccc;
            box-shadow: none;
        }
        &:focus {
            border-radius: 7px;
            border: 1px solid @input-border-focus;
            box-shadow: none;
        }
    }
    //&:focus{ outline: none; }
    &[resizable] {
        resize: both;
    }
}

.textareaHiddenDiv {
    position: absolute;
    top: -9999px;
    left: -9999px;
    visibility: hidden;
    white-space: pre-wrap;
    padding: 0 0 0 10px;
    word-wrap: break-word;
}

.content-scroll {
    .n-textarea {
        border: none;
        width: 100%;
        resize: none;
        margin: 0;
        padding: 0 10px 0 10px;
        overflow: hidden;
        margin: 5px 0;
        outline: none;

        &:hover {
            border: none;
            box-shadow: none;
        }
    }

    .textarea-wrapper {
        overflow: hidden;
        border: 1px solid #B2B2B2;
        box-shadow: none;
        border-radius: 7px;
        padding: 1px;
        background-color: white;

        &[disabled]:hover {
            border: 1px solid #B2B2B2;
            box-shadow: none;
        }
    }

    .textarea-wrapper-normal {
        border: 1px solid #b2b2b2;
        &:hover {
            border-radius: 7px;
            border: 1px solid #00a1cc;
            box-shadow: 0 1px 0 #ffffff, inset 0 0 0 1px #66dfff;
        }
    }

    .textarea-wrapper-focus {
        border: 1px solid @input-border-focus;
    }

    .mCustomScrollBox {
        border-radius: 5px;
        padding: 0;
        width: 100%;

        > .mCSB_container {
            margin-right: 0;
        }

        > .mCSB_scrollTools {
            box-sizing: border-box;
            padding: 5px 0;

            > .mCSB_draggerContainer {
                margin: 5px 0;
            }
        }
    }
}