@import "../bower_components/bootstrap/less/buttons.less";
@import "variables";


@n-toggle-button-active-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
@n-toggle-button-active-shadow-dark: inset 0 3px 5px rgba(0, 0, 0, .375);
@n-toggle-button-toggle-on-shadow:inset 0px 1px 1px 0px lighten(#000000, 30%),  inset 0px -1px 0px 0px #eeeeee;
@n-toggle-button-active-color-start:#585858;
@n-toggle-button-active-color-end:#777777;
@n-toggle-button-hover-border-color:#666;
@n-toggle-button-default-dotted-border:1px dotted @n-toggle-button-hover-border-color;

.n-toggle-button {

  &.active {

    #gradient.vertical(@n-toggle-button-active-bg-grey-top, @n-toggle-button-active-bg-grey-bottom);
    color: @gray-global;
    border:@button-default-border;
    border-color: @gray-light;
    text-shadow: none;
    box-shadow: @n-toggle-button-toggle-on-shadow;

    .icon:before {
      color: @gray-global;
    }

    &:focus {
      border:@button-default-border;
      border-color: @gray-global;
    }

    &:hover {
      #gradient.vertical(@n-toggle-button-active-color-start, @n-toggle-button-active-color-end) ;
      color:@gray-white;
      border-left:@n-toggle-button-default-dotted-border;
      border-right:@n-toggle-button-default-dotted-border;
      border-color:@n-toggle-button-hover-border-color;
      box-shadow:none;
      .icon:before {
        color: @gray-white;
      }
    }

    &:active {
      #gradient.vertical(@n-toggle-button-active-bg-grey-top, @n-toggle-button-active-bg-grey-bottom);
      color: @gray-global;
      border:@button-default-border;
      border-color: @gray-light;
      text-shadow: none;
      box-shadow: @n-toggle-button-toggle-on-shadow;
      .icon:before {
        color: @gray-global;
      }
    }
  }
  &:active {
    #gradient.vertical(@n-toggle-button-active-bg-grey-top, @n-toggle-button-active-bg-grey-bottom);
    color: @gray-global;
    border-color: @gray-light;
    text-shadow: none;
    box-shadow: @n-toggle-button-toggle-on-shadow;
    .icon:before {
      color: @gray-global;
    }
  }


  &-small {
    height: 26px;
    padding-top: 2px;
  }

  &-dark.active {
    #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
    color: @gray-white;
    .icon:before {
      color: @gray-white;
    }
  }

  &-color.active {
    #gradient.vertical(@n-toggle-button-active-bg-green-top, @n-toggle-button-active-bg-green-bottom);
    color: @gray-white;
    .icon:before {
      color: @gray-white;
    }
    &,
    &:focus,
    &:hover {
      box-shadow: @n-toggle-button-active-shadow-dark;
    }
  }

}