//
// Variables
// --------------------------------------------------

//== Colors
//

//## Brand colors from OT3 specification
@gray-global: #474747;
@gray-nav: #333333;
@gray-dark: #1B1B1B;
@gray-medium: #666666;
@gray-light: #AAAAAA;
@gray-disabled: #999999;
@gray-white: #ffffff;

@blue-nokia: #124191;
@blue-selection: #0B6DAF;
@blue-hover: #00A1CC;

@black-hover: #000000;

@brand-primary: @blue-selection;
@brand-success: #3DAA00;
@brand-info: #68717A;
@brand-warning: #FFCC00;
@brand-danger: #BE0006;
@brand-danger-background: #F2DDDC;

@fault-critical: #f20d0e;
@fault-major: #f77e00;
@fault-minor: @brand-warning;
@fault-warning: #4847e6;
@fault-cleared: #68cc00;
@fault-unknown: @gray-disabled;

@gradient-background-linear-top: #d2d4dc;
@gradient-background-linear-bottom: #efefef;

@gradient-background-radial-center: #fbfbfb;
@gradient-background-radial-far-side: #c4c7d0;

@gradient-background-dialog-top: #fefefe;
@gradient-background-dialog-bottom: #efefef;

//== Scaffolding
//
//## Settings for some of the most global styles.

//** Background color for `<body>`.
@body-bg: #efefef;

//** Global text color on `<body>`.
@text-color: @gray-dark;

//** Global textual link color.
@link-color: @blue-selection;
//** Link hover color set via `darken()` function.
@link-hover-color: @blue-hover;

//== Typography
//
//## Font, line-height, and color for body text, headings, and more.

@font-family-sans-serif: "Nokia Pure", Helvetica, Arial, sans-serif;
@font-family-serif: "Nokia Pure", Georgia, "Times New Roman", Times, serif;
//** Default monospace fonts for `<code>`, `<kbd>`, and `<pre>`.
@font-family-monospace: Menlo, Monaco, Consolas, "Courier New", monospace;
@font-family-base: @font-family-sans-serif;

@font-size-base: 14px;
@font-size-max: 40px;  // ~40px
@font-size-large: 20px;  // ~20px
@font-size-medium: 16px;  // ~16px
@font-size-small: 12px;  // ~12px
@font-size-product: 21px;  // ~21px
@font-size-banner-condensed: 18px;
@font-size-login:24px;

@font-size-h1: @font-size-max;
@font-size-h2: @font-size-product;
@font-size-h3: @font-size-large;
@font-size-h4: @font-size-medium;
@font-size-h5: @font-size-base;
@font-size-h6: @font-size-small;

//** Unit-less `line-height` for use in components like buttons.
@line-height-base: 1.428571429;
// 20/14
//** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
@line-height-computed: floor((@font-size-base * @line-height-base));
// ~20px

//** By default, this inherits from the `<body>`.
@headings-font-family: inherit;
@headings-font-weight: 500;
@headings-line-height: 1.1;
@headings-color: inherit;

//== Components
//
//## Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).

@padding-base-vertical: 6px;
@padding-base-horizontal: 11px;

@padding-large-vertical: 10px;
@padding-large-horizontal: 16px;

@padding-small-vertical: 4px;
@padding-small-horizontal: 10px;

@padding-xs-vertical: 1px;
@padding-xs-horizontal: 5px;

@line-height-large: 1.33;
@line-height-small: 1.5;

@container-sm-no-gutter: (@container-sm - @grid-gutter-width);
@container-md-no-gutter: (@container-md - @grid-gutter-width);
@container-lg-no-gutter: (@container-lg - @grid-gutter-width);
@n-brand-container-lg: 1400px;
//== Forms
//
//##

//** `<input>` background color
@input-bg: white;
//** `<input disabled>` background color
@input-bg-disabled: #f3f3f3;

//** Text color for `<input>`s
@input-color: @gray-medium;
@input-color-disabled: @gray-light;

//** Text color for input fields, different from checkbox
@text-input-color-selected-font: white;
//** Placeholder text color
@input-color-placeholder: @gray-global;

//** `<input>` border color, used e.g. checkbox border
@input-border: #B2B2B2;
//** Border color for inputs on focus and active
@input-border-focus: @gray-medium;
//** Border color for inputs on hover
@input-border-hover: @blue-hover;
//** Border color for disabled inputs
@input-border-disabled: #CCCCCC;
//** border radius for fields - if you need different ones update here
@input-border-radius: 7pt;

//** Default `.form-control` height
@input-height-base: (@line-height-computed + (@padding-base-vertical * 2) + 2);
//** Large `.form-control` height
@input-height-large: (ceil(@font-size-large * @line-height-large) + (@padding-large-vertical * 2) + 2);
//** Small `.form-control` height
@input-height-small: (floor(@font-size-small * @line-height-small) + (@padding-small-vertical * 2) + 2);

@legend-color: @gray-dark;
@legend-border-color: #e5e5e5;

//** Background color for textual input addons
@input-group-addon-bg: @gray-lighter;
//** Border color for textual input addons
@input-group-addon-border-color: @input-border;

//** Border color for textual input addons
@dropdown-select-caret-border-color: @input-border;

//** Disabled cursor for form controls and buttons.
@cursor-disabled: default;

//== Buttons
