@import "../bower_components/bootstrap/less/navs.less";
@import "variables.less";

// OT3 p.145
// Tabs

@tab-hover-shaow:inset 0px 1px 1px 0px lighten(#00a1cc, 30%);

.nav-tabs {
    margin-bottom: 0;
    > li {
        > a {
            // Normal tabs that are not on top
            font-weight: normal;
            color: @nav-tabs-text-color;
            border-radius: @nav-tabs-border-radius @nav-tabs-border-radius 0 0;
            padding: 5px 10px 5px 10px;
            margin: 0;
            border-color: @nav-tabs-border-color;
            #gradient > .vertical(@nav-tabs-gradient-bottom, @nav-tabs-gradient-top);

            &:hover {
                box-shadow: @tab-hover-shaow;
            }
            // Accessed via right click
            &:focus {
                #gradient > .vertical(@nav-tabs-focus-gradient-start, @nav-tabs-focus-gradient-end);
                color: @nav-tabs-focus-text-color;
                border-color: @nav-tabs-border-color;
                box-shadow: none;
                outline: none !important;
            }
            // When clicked and button kept down
            &:active {
                //#gradient > .vertical(@nav-tabs-dark-gradient-top, @nav-tabs-dark-gradient-bottom);
                #gradient > .vertical(@nav-tabs-gradient-bottom, @nav-tabs-gradient-top);
                color: @nav-tabs-text-color;
                &:hover {
                    border-color: @nav-tabs-border-color;
                    box-shadow: none;
                }

                &:active {
                    #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
                    border-color: @button-active-border-color;
                    border-top-color: @button-active-top-border-color;
                    text-shadow: @button-text-shadow-white;
                    box-shadow: @button-drop-shadow-active;
                }
            }
        }
        &[disabled] > a,
        &.disabled > a {
            #gradient > .vertical(@nav-tabs-disabled-gradient-bottom, @nav-tabs-disabled-gradient-top);
            color: @gray-light;
            border-color: @nav-tabs-border-disabled;
            border-bottom-color: @nav-tabs-border-color;
            &:hover {
                border-color: @nav-tabs-border-disabled;
                border-bottom-color: @nav-tabs-border-color;
                box-shadow: none;
            }
        }

        // Currently shown tab on top
        &.active > a {
            &,
            &:hover,
            &:focus {
                #gradient > .vertical(@nav-tabs-gradient-top, @nav-tabs-gradient-mid);
                background-color: @nav-tabs-gradient-mid;
                color: @nav-tabs-text-color;
                border: 1px solid @nav-tabs-border-color;
                border-bottom-color: transparent;
                box-shadow: none;
            }
        }
    }
}

.tab-content {
    #gradient > .vertical(@nav-tabs-gradient-mid, @nav-tabs-gradient-bottom);
    border-left: 1px solid @nav-tabs-border-color;
    border-right: 1px solid @nav-tabs-border-color;
    border-bottom: 1px solid @nav-tabs-border-color;
    border-radius: 0 0 @nav-tabs-border-radius @nav-tabs-border-radius;
    padding: 10px;
    box-shadow:0px 1px 3px 0px @nav-tabs-dark-gradient-top;
}

.tab-content-scroll{
    padding: 3px 2px;
    height: 100px;

    .tab-pane {
        display: none;
        visibility: hidden;
    }
    .active {
        display: block;
        visibility: visible;
        padding: 7px 8px;
    }

    >.mCustomScrollBox{
        border-radius:0px;
        padding: 0;
        width: 100%;
        background: transparent;

        .mCSB_container{
            margin-right: 0;
            padding-right: 0;
        }
    }
}

.n-tabbed-spacekkey-active{
    #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end) !important;
    color: @nav-tabs-text-color !important;
    border-color: @button-active-border-color !important;
    border-top-color: @button-active-top-border-color !important;
    text-shadow: @button-text-shadow-white !important;
    box-shadow: @button-drop-shadow-active !important;
}